<template>
  <div class="province-wrapper">
    <div class="province-header">
      <!-- <h5 class="header-text">Select Province</h5> -->
      <div class="select-box form-field sign-up w-input">
        <div @click="toggleOptions()" class="select-header">
          <span :style="defaultTextStyle">{{
              selectedProvince == "" ? "Select Province" : selectedProvince
            }}</span>
          <img
              :class="['caret', { 'rotate-caret': optionsOpen }]"
              src="@/assets/img/caret-up.svg"
              alt=""
          />
        </div>
        <div
            v-show="store.openOptions.provinces"
            class="select-options"
            @mouseenter="() => hoverUpdate('in')"
            @mouseleave="() => hoverUpdate('out')"
        >
          <ul>
            <li
                v-for="(option, index) in provinceOptions"
                :key="option + index"
                @click="($event) => select($event.target.innerHTML)"
            >
              {{ option }}
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Provinces from "@/utils/Address/canadian-provinces.json";

import { store } from "@/components/auth/SignUpPage.vue";

export default {
  name: "ProvinceComponent",
  // props: {
  //   modelValue: {
  //     type: String,
  //     required: true,
  //   },
  // },
  data() {
    return {
      selectedProvince: "",
      optionsOpen: store.openOptions.provinces,
      provinceOptions: [],
      store: store,
      hover: false,
      timeoutStore: "",
    };
  },
  methods: {
    hoverUpdate(event) {
      if (event == "in") {
        this.hover = true;
      } else if (event == "out") {
        this.hover = false;
      }
    },
    toggleOptions() {
      if (this.store.openOptions.provinces) {
        this.store.closeAll();
        clearInterval(this.timeoutStore);
      } else {
        this.store.closeAll();
        this.store.openSingleOption("provinces");
        this.timeoutStore = setInterval(() => {
          if (!this.hover) {
            this.store.closeAll();
            clearInterval(this.timeoutStore);
          }
        }, 5000);
      }
    },
    select(province) {
      this.selectedProvince = province;
      this.toggleOptions();
      this.updateModel();
      clearInterval(this.timeoutStore);
    },
    updateModel() {
      this.$emit("updateModel", this.selectedProvince);
    },
  },
  mounted() {
    let provinceNames = Provinces.map((province) => province.name);
    this.provinceOptions = provinceNames;
    this.updateModel();
  },
  computed: {
    defaultTextStyle() {
      if (this.selectedProvince === "") {
        return { color: "#8F95B2" };
      } else {
        return "";
      }
    },
  },
};
</script>

