      keynoteread: KeynoteRequest.readById,
      projectionmodel: ProjectionRequest.create,
      projectionread: ProjectionRequest.readById,
      tractionmodel: TractionRequest.create,
      tractionread: TractionRequest.readById,
      campaignModel: CampaignRequest.readByCompanyId,
      chart: false,
      campaignExists: true,
    };
  },
  methods: {
    goToAddCampaign() {
      this.RouterUtils.changeRouteTo(
        this.RouterUtils.routes.company.ADD_CAMPAIGN.name
      );
    },
    goToDashboard() {
      this.value = "dashboard";
    },
    goToCompany() {
      this.value = "company";
    },
    goToWallet() {
      this.value = "wallet";
    },
    goToCampaign() {
      this.campaignModel.campaignCompanyId = localStorage.getItem("id");
      this.$store.dispatch(
        "campaign/readCampaignByCompanyId",
        this.campaignModel
      );
      this.value = "campaign";
    },
    uploadImageFile(imageType) {
      // console.log(this.$refs["filelogo"]);
      let refName = imageType.slice(0, 4);
      let input = this.$refs["file" + refName];
      console.log(input)
      // console.log("input", input, "imageType", imageType, "refName", refName);
      let files = input.files;
      console.log(files)
      this.fileName = files[0].name;
      if (files[0] !== null) {
        this.awaitingUpload = false;
        this.downloadUpload = true;
        setTimeout(() => {
          document.querySelector(".progress-bar").style.width = "50%";
          document.getElementById("status").innerHTML = "50%";
        }, 2000);
        setTimeout(() => {
          document.querySelector(".progress-bar").style.width = "100%";
          document.getElementById("status").innerHTML = "100%";
        }, 3000);
        setTimeout(() => {
          this.downloadUpload = false;
          this.finishUpload = true;
        }, 4000);
      }
      let size = files[0].size / 1000000;

      if (size > 5) {
        swal(
          "Error",
          "File must not be moer than 5mb, please try with a reduced file size",
          "error"
        );
        return;
      }
      const reader = new FileReader();

      try {
        reader.onload = (e) => {
          this.base64 = e.target.result;
          this.uploadOfficerImagee(imageType);
        };
        reader.readAsDataURL(files[0]);
        this.$emit("input", files[0]);
      } catch (e) {
        console.warn(e.message);
      }
    },
    finishUploading() {
      console.log(this.company.companyInfo);
    },
