          this.downloadUpload = false;
          this.finishUpload = true;
        }, 4000);
      }
      let input = this.$refs.file;
      console.log(input);
      let files = input.files;
      this.fileName = files[0].name;
      // console.log(this.fileName);
      if (this.fileName === "Qoovest_Data_Templates.xlsx") {
        readXlsxFile(file.files[0]).then((rows) => {
          this.items = rows;
          // console.log(this.items)
        });
        readXlsxFile(this.$refs.file.files[0], { sheet: "Company" }).then(
          (data) => {
            this.items2 = data;
            console.log(this.items2[0][3]);
            this.uploadModel.companyUploadCompanyId =
