      this.showLoader = true;
      // this.model.username = `${this.name}_${this.auth.userInfo.customerUsername}_${Date.now()}`;
      this.model.username = `${
        this.auth.userInfo.userFirstName + this.auth.userInfo.userLastName
      }_${Date.now()}${type}`;
      this.model.base64 = this.base64;
      await this.$store.dispatch("sbucket/uploadEncodedFile", this.model, {
        root: true,
      });
      this.url = this.sbucket.s3bucketResponse.url;
      let modelObject = {};
      let updateType = "";
      switch (type) {
        case "banner":
          updateType = "companyBannerImage";
          break;
        case "picture":
          updateType = "companyLogo";
          break;
        case "card-image":
          updateType = "companyCardImage";
          break;
        case "form-A":
          updateType = "companyFormM";
          break;
      }
      modelObject.companyId = this.company.company.data.companyId;
      modelObject[updateType] = this.url;
      this.$store.dispatch("company/updateCompany", modelObject);
      this.showLoader = false;
