<script>
import HomeLayout from "@/views/layout/HomeLayout";
import S3Request from "@/models/requests/S3Request";
import {mapState} from "vuex";
import CampaignRequest from "@/models/requests/CampaignRequest";
import swal from "sweetalert";
import RouterUtils from "@/utils/BaseUtils/RouterUtils";

export default {
  name: "AddCampaignDetails",
  components: {HomeLayout},
  data(){
    return {
      file: null,
      fileName: null,
      awaitingUpload: true,
      downloadUpload: false,
      finishUpload: false,
      items: [],
      url:"",
      disableInput: false,
      showLoader: false,
      base64: "",
      model: S3Request.prototype.uploadBase64(),
      updateModel: CampaignRequest.update
    }
  },
  computed: {
    ...mapState(["campaign", "auth", "company"]),
    updatedCampaign: state => state.campaign.updated
  },
  methods: {
    goToDashboard(){
      RouterUtils.changeRouteTo(
          RouterUtils.routes.company.DASHBOARD.name
      )
    },
    campaignPreview(){
      RouterUtils.changeRouteTo(
          RouterUtils.routes.company.CAMPAIGN_PREVIEW.name
      )
    },
    close(className){
      document.querySelector(className).style.display='none';
      this.awaitingUpload = true
    },
    uploadProfile(className){
      document.querySelector(className).style.display='flex'
    },
    uploadImageFile(imageType) {
      console.log(this.$refs["filelogo"]);
      let refName = imageType.slice(0, 4);
      let input = this.$refs["file" + refName];
      console.log("input", input, "imageType", imageType, "refName", refName);
      let files = input.files;
      this.fileName = files[0].name;
      if (files[0] !== null) {
        this.awaitingUpload = false;
        this.downloadUpload = true;
        setTimeout(() => {
          document.querySelector(".progress-bar").style.width = "50%";
          document.getElementById("status").innerHTML = "50%";
        }, 2000);
        setTimeout(() => {
          document.querySelector(".progress-bar").style.width = "100%";
          document.getElementById("status").innerHTML = "100%";
        }, 3000);
        setTimeout(() => {
          this.downloadUpload = false;
          this.finishUpload = true;
        }, 4000);
      }
      let size = files[0].size / 1000000;

      if (size > 5) {
        swal(
            "Error",
            "File must not be moer than 5mb, please try with a reduced file size",
            "error"
        );
        return;
      }
      const reader = new FileReader();

      try {
        reader.onload = (e) => {
          this.base64 = e.target.result;
          this.uploadOfficerImagee(imageType);
        };
        reader.readAsDataURL(files[0]);
        this.$emit("input", files[0]);
      } catch (e) {
        console.warn(e.message);
      }
    },
    async uploadOfficerImagee(type) {
      this.showLoader = true;
      // this.model.username = `${this.name}_${this.auth.userInfo.customerUsername}_${Date.now()}`;
      this.model.username = `${
          this.auth.userInfo.userFirstName + this.auth.userInfo.userLastName
      }_${Date.now()}${type}`;
      this.model.base64 = this.base64;
      await this.$store.dispatch("sbucket/uploadEncodedFile", this.model, {
        root: true,
      });
      this.url = this.sbucket.s3bucketResponse.url;
      let modelObject = {};
      let updateType = "";
      switch (type) {
        case "banner":
          updateType = "companyBannerImage";
          break;
        case "logo":
          updateType = "companyLogo";
          break;
        case "card-image":
          updateType = "companyCardImage";
          break;
        case "campaign":
          updateType = "campaignCardImage";
      }
      modelObject.companyId = this.company.company.data.companyId;
      modelObject[updateType] = this.url;
      this.updateModel.campaignCardImage = this.url
      this.$store.dispatch("campaign/updateCompany", modelObject);
      this.showLoader = false;
    },
    submit(){
      this.updateModel.campaignId = this.campaign.campaign.data.campaignId;
      this.updateModel.campaignCompanyId = this.campaign.campaign.data.campaignCompanyId;
      this.updateModel.campaignName = this.campaign.campaign.data.campaignName;
      this.$store.dispatch("campaign/updateCompany", this.updateModel);
    }
  },
  watch: {
    updatedCampaign(){
      if (this.updatedCampaign) {
        this.RouterUtils.changeRouteTo(
            this.RouterUtils.routes.company.DASHBOARD.name
        );
      }
    }
  },
}
</script>

