<script>
import HomeLayout from "@/views/layout/HomeLayout";
import RouterUtils from "@/utils/BaseUtils/RouterUtils";
import { mapState } from "vuex";
import CompanyRequest from "@/models/requests/CompanyRequest";
import UserRequest from "@/models/requests/UserRequest";
export default {
  name: "theHeaderUser",
  components: { HomeLayout },
  data() {
    return {
      RouterUtils: RouterUtils,
      changeValue: "",
      companyExist: false,
      model: CompanyRequest.readById,
      usermodel: CompanyRequest.readByUserId,
      updatemodal : UserRequest.readById,
      totalCompanies: null,
      companymodel: CompanyRequest.readByUserId
    };
  },
  methods: {
    goToVerify(){
      this.RouterUtils.changeRouteTo(
          this.RouterUtils.routes.company.SETTINGS.name
      )
    },
    goToExplore(){
      this.RouterUtils.changeRouteTo(
          this.RouterUtils.routes.company.EXPLORE.name
      )
    },
    goToRaiseFunds(){
      this.RouterUtils.changeRouteTo(
          this.RouterUtils.routes.user.FUNDS.name
      )
    },
    goToAbout(){
      this.RouterUtils.changeRouteTo(
          this.RouterUtils.routes.home.ABOUT.name
      )
    },
    goToFAQ(){
      this.RouterUtils.changeRouteTo(
          this.RouterUtils.routes.home.FAQ.name
      )
    },
    goToWallet(){
      this.$store.dispatch("company/updateSettings", {})
      this.RouterUtils.changeRouteTo(
          this.RouterUtils.routes.company.SETTINGS.name
      );
    },
    goToSetting() {
      this.wallet = true;
      this.RouterUtils.changeRouteTo(
          this.RouterUtils.routes.company.SETTINGS.name
      );
      this.wallet = true;
    },
    goToAddCompany() {
      if (document.getElementById("dropdown1").classList.contains("w--open")) {
        document.getElementById("dropdown1").classList.remove("w--open");
      }
      this.RouterUtils.changeRouteTo(
          this.RouterUtils.routes.company.ADD_COOMPANY.name
      );
    },
    openNavbar() {
      if (!document.getElementById("dropdown1").classList.contains("w--open")) {
        document.getElementById("dropdown1").classList.add("w--open");
      } else {
        document.getElementById("dropdown1").classList.remove("w--open");
      }
    },
    logout() {
      localStorage.clear();
      document.location = "/";
    },
    pageChecker() {
      if (this.$route.path === "/user/explore") {
        document.querySelector(".explore").style.color = "#5DC9A0";
      }
      if (this.$route.path === "/user/raising-fund") {
        document.querySelector(".raise-fund").style.color = "#5DC9A0";
      }
      if (this.$route.path === "/about-us") {
        document.querySelector(".about").style.color = "#5DC9A0";
      }
      if (this.$route.path === "/faq") {
        document.querySelector(".faq").style.color = "#5DC9A0";
      }
    },
    // checkCompany() {
    //   if (this.company.userCompany) {
    //     this.companyExist = true;
    //   }
    // },
    getId(event) {
      console.log(event);
      let allCompanies = this.company.userCompany;
      console.log(this.company);
      allCompanies.forEach((company) => {
        console.log(event.target.innerText);
        console.log(company.companyName);
        if (event.target.innerText === company.companyName) {
          console.log(event.target.innerText);
          this.model.companyId = company.companyId;
          localStorage.setItem("id", company.companyId);
          this.$store.dispatch("company/readCompanyById");
          this.RouterUtils.changeRouteTo(
              this.RouterUtils.routes.company.DASHBOARD.name
          );
        }
        else {
          console.log("they are not the same")
        }
      });
    },
  },
  mounted() {
    this.totalCompanies = this.auth.userInfo.data.length
    console.log(this.company);
    this.updatemodal.userId = this.auth.userInfo.userId
    this.$store.dispatch('user/readUserById', this.updatemodal)
    this.companymodel.companyUserId = this.auth.userInfo.userId
    this.$store.dispatch('company/readCompanyByUserId', this.companymodel)
    // document
    //   .querySelector(".wf-section")
    //   .addEventListener("click", function () {
    //     document.getElementById("dropdown1").classList.remove("w--open");
    //   });
    this.pageChecker();
  },
  computed: {
    ...mapState(["campaign", "auth", "company"]),
    userCompany: state => state.company.userCompany,
    userInfo: state => state.auth.userInfo,
  },
  watch: {
    userCompany(){
      console.log("what diid i do")
