import StoryResponse from "@/models/response/StoryResponse";
import StoryService from "@/services/StoryService";
import StoryRequest from "@/models/requests/StoryRequest";
import BaseNotification from "@/components/baseNotification/BaseNotification";

export const state = {
    loading: false,
    story: StoryResponse.story,
    stories: StoryResponse.read
}

export const getters = {

}

export const mutations = {
    updateLoading: (state, payload) => {
        state.loading = payload;
    },
    updateStory: (state, payload) => {
        state.story = payload
    },
    updateStories: (state, payload) => {
        state.stories = payload;
    },
}

export const actions = {
    createStory({commit},payload = StoryRequest.create){
        commit("updateLoading", true)
        return StoryService.callCreateApi(payload)
            .then(response => {
                commit("updateLoading", false)
                let responseData = response.data
                if(responseData.responseCode === "00"){
                    BaseNotification.fireToast("success", responseData.responseMessage).then()
                    commit('updateScreen', 'success')
                }else{
                    BaseNotification.fireToast("error", responseData.responseMessage).then()
                }
            })
            .catch(err=>{
                commit("updateLoading", false)
                console.error(err)
            })
    },
    updateStory: ({ commit }, payload = StoryRequest.read) => {
        commit('updateLoading', true)
        return StoryService.callUpdateApi(payload).then(response => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateLoading", false)
                commit("updateStory", responseData)
                // BaseNotification.fireToast("success", responseData.responseMessage).then()

            } else {
                commit("updateLoading", false)
                BaseNotification.fireToast("error", responseData.responseMessage).then()
            }
        }).catch((error) => {
            commit("updateLoading", false)
            BaseNotification.fireToast("success", error).then()


        })

    },
    readStory({commit}, payload = StoryRequest.read) {
        return StoryService.callReadApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateAddedStory", responseData)
            }
        })
    },
    deleteStory(){

    }
}
