<template>
  <div class="target-input">
    <div class="ment-button" @click="decrement()">-</div>
    <input
        id="duration-input"
        type="number"
        :value="days"
        @input="fillDuration"
        @keyup.enter="validateInput"
        required
    />
    <div class="ment-button" @click="increment()">+</div>
    Days
  </div>
</template>

<script>
export default {
  name: "CampaignDuration",
  data() {
    return {
      days: 60
    };
  },
  methods: {
    increment() {
      if (this.days < 180){
        this.days++
        this.$emit('campaign-duration', this.days)
      }
       else false;
    },
    decrement() {
      if (this.days > 60){
        this.days--
        this.$emit('campaign-duration', this.days)
      }
      else false;
    },
    fillDuration(e) {
      let input = e.target.value;
      console.log(input)
      if (typeof +input == "number" && input <= 180 && input >= 60) {
        this.days = +input;
      }
      console.log(+input);
    },
    validateInput(e) {
      let input = e.target.value;
      console.log(input)
      if (typeof +input == "number" && input <= 180 && input >= 60) {
        this.days = +input;
      } else {
        this.days = 60;
      }
    },
  },
};
</script>

