<template>
  <div class="phone-container">
    <div @click="toggleOptions()" class="phone-country-header">
      <span class="flag-text"
        ><img
          src="https://flagcdn.com/16x12/ca.png"
          srcset="
            https://flagcdn.com/32x24/ca.png 2x,
            https://flagcdn.com/48x36/ca.png 3x
          "
          width="16"
          height="12"
          alt="Canada"
        />
      </span>
      <span class="code-text">+1</span>
      <!-- <img class="caret" src="@/assets/img/caret-up.svg" /> -->
      <div
        v-show="store.openOptions.phone"
        class="options-list"
        @mouseenter="() => hoverUpdate('in')"
        @mouseleave="() => hoverUpdate('out')"
      >
        <ul>
          <li v-for="(option, index) in options" :key="option.name + index">
            <span class="flag-text-2"
              ><img
                src="https://flagcdn.com/16x12/ca.png"
                srcset="
                  https://flagcdn.com/32x24/ca.png 2x,
                  https://flagcdn.com/48x36/ca.png 3x
                "
                width="16"
                height="12"
                alt="Canada"
            /></span>
            <span class="country-name"> {{ option.name }}</span>
          </li>
        </ul>
      </div>
    </div>
    <input
      placeholder="Phone Number"
      v-model="number"
      @input="emitValue()"
      type="number"
      required=""
    />
  </div>
</template>

<script>
import getCountryData from "@/utils/country/countryFlags";

import { store } from "@/components/auth/SignUpPage.vue";

export default {
  name: "PhoneNumberInput",
  data() {
    return {
      store: store,
      country: "canada",
      options: [
        {
          name: "Canada",
          flag: "",
        },
      ],
      optionsOpen: store.openOptions.phone,
      number: "",
      hover: false,
      timeoutStore: "",
    };
  },
  computed: {
    flagText() {
      let flagObject = getCountryData(this.country);
      return flagObject.flag;
    },
  },
  methods: {
    hoverUpdate(event) {
      if (event == "in") {
        this.hover = true;
      } else if (event == "out") {
        this.hover = false;
      }
    },
    toggleOptions() {
      if (this.store.openOptions.phone) {
        this.store.closeAll();
        clearInterval(this.timeoutStore);
      } else {
        this.store.closeAll();
        this.store.openSingleOption("phone");
        this.timeoutStore = setInterval(() => {
          if (!this.hover) {
            this.store.closeAll();
            clearInterval(this.timeoutStore);
          }
        }, 5000);
      }
    },
    emitValue() {
      this.$emit("numberValue", {
        country: this.country,
        number: this.number,
      });
    },
  },
};
</script>

