import client from "./BaseService";

export default {
    callCreateApi(payload) {
        return client.apiClient.post("/campaign/create", payload);
    },
    callReadApi(payload) {
        return client.apiClient.post("/campaign/read", payload);
    },
    callReadByIdApi(payload) {
        return client.apiClient.post("/campaign/read-by-campaign-id", payload);
    },
    callReadByCompanyIdApi(payload) {
        return client.apiClient.post("/campaign/read-by-company-id", payload);
    },
    callUpdateApi(payload){
        return client.apiClient.post("/campaign/update", payload);
    },
    callDeleteApi(payload){
        return client.apiClient.post("/campaign/delete", payload);
    }

}