import InstructionResponse from "@/models/response/InstructionResponse";
import InstructionService from "@/services/InstructionService";
import InstructionRequest from "@/models/requests/InstructionRequest";
import BaseNotification from "@/components/baseNotification/BaseNotification";

export const state = {
    loading: false,
    instruction: InstructionResponse.instruction,
    instructions: InstructionResponse.read
}

export const getters = {

}

export const mutations = {
    updateLoading: (state, payload) => {
        state.loading = payload;
    },
    updateInstruction: (state, payload) => {
        state.instruction = payload
    },
    updateInstructions: (state, payload) => {
        state.instructions = payload;
    },
}

export const actions = {
    createInstruction({commit},payload = InstructionRequest.create){
        commit("updateLoading", true)
        return InstructionService.callCreateApi(payload)
            .then(response => {
                commit("updateLoading", false)
                let responseData = response.data
                if(responseData.responseCode === "00"){
                    BaseNotification.fireToast("success", responseData.responseMessage).then()
                }else{
                    BaseNotification.fireToast("error", responseData.responseMessage).then()
                }
            })
            .catch(err=>{
                commit("updateLoading", false)
                console.error(err)
            })
    },

    editInstruction: ({ commit }, payload = InstructionRequest.read) => {
        commit('updateLoading', true)
        return InstructionService.callUpdateApi(payload).then(response => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateLoading", false)
                commit("updateInstruction", responseData)
                // BaseNotification.fireToast("success", responseData.responseMessage).then()

            } else {
                commit("updateLoading", false)
                BaseNotification.fireToast("error", responseData.responseMessage).then()
            }
        }).catch((error) => {
            commit("updateLoading", false)
            BaseNotification.fireToast("success", error).then()


        })

    },

    readInstruction({commit}, payload = InstructionRequest.read) {
        return InstructionService.callReadApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateInstructions", responseData)
            }
        })
    },
    deleteInstruction(){

    }
}
