<script>
import HomeLayout from "@/views/layout/HomeLayout.vue";
import { mapState } from "vuex";
import RouterUtils from "@/utils/BaseUtils/RouterUtils";
import swal from "sweetalert";
import CampaignTargetInput from "./CampaignTargetInput.vue";
import CampaignDuration from "./CampaignDuration.vue";
import CampaignEquitySelect from "./CampaignEquitySelect.vue";
import CampaignRequest from "@/models/requests/CampaignRequest";

export default {
  name: "AddCampaignPage",
  components: {
    HomeLayout,
    CampaignTargetInput,
    CampaignDuration,
    CampaignEquitySelect,
  },
  data() {
    return {
      value: true,
      model: CampaignRequest.create,
      updateModel: CampaignRequest.update,
      RouterUtils: RouterUtils,
      url: "",
      disableInput: false,
      showLoader: false,
      fileName: "",
      base64: "",
      displayedTab: "info",
    };
  },
  mounted() {
    this.displayInfo();
    console.log(this.createdCampaign);
    if (this.campaign.campaign.data.campaignName) {
      this.$store.commit("campaign/updateCreated", true);
    }
  },
  methods: {
    createCampaign() {
      this.model.campaignCompanyId = this.company.company.data.companyId;
      if (
        this.model.campaignTarget < 50000 ||
        this.model.campaignTarget > 5000000
      ) {
        alert("The target is less than 5000 or greater than 5000000");
      } else {
        this.$store.dispatch("campaign/createCampaign", this.model);
      }
    },
    updateCampaign() {
      this.updateModel.campaignId = this.campaign.campaign.data.campaignId;
      this.updateModel.campaignName = this.campaign.campaign.data.campaignName;
      this.updateModel.campaignCompanyId = this.company.company.data.companyId;
      this.$store.dispatch("campaign/updateCampaign", this.updateModel);
    },
    getEquity(equity) {
      this.model.campaignList = equity;
      console.log(equity);
    },
    getCampaignTarget(target) {
      this.model.campaignTarget = target;
      console.log(target);
    },
    getCampaignDuration(duration) {
      this.model.campaignDuration = duration;
      console.log(duration);
    },
    displayInfo() {
      this.displayedTab = "info";
      console.log(this.$refs.infoTab);
      this.$refs.infoTab.style.display = "block";
      this.$refs.descriptionTab.style.display = "none";
      this.$refs.infoIndicator.classList.add("active");
      this.$refs.infoIndicator.classList.remove("inactive");
      this.$refs.descriptionIndicator.classList.remove("active");
      this.$refs.descriptionIndicator.classList.add("inactive");
      this.$refs.infoText.classList.add("active");
      this.$refs.descText.classList.remove("active");
    },
    displayDescription() {
      this.displayedTab = "description";
      this.$refs.infoTab.style.display = "none";
      this.$refs.descriptionTab.style.display = "block";
      this.$refs.infoIndicator.classList.remove("active");
      this.$refs.infoIndicator.classList.add("inactive");
      this.$refs.descriptionIndicator.classList.add("active");
      this.$refs.descriptionIndicator.classList.remove("inactive");
      this.$refs.infoText.classList.remove("active");
      this.$refs.descText.classList.add("active");
    },
    checkSuccessful() {
      localStorage.setItem("name", this.model.companyName);
      this.model.companyUserId = this.auth.userInfo.userId;
      this.usermodel.companyUserId = this.auth.userInfo.userId;
      this.model.companyCardImage = this.url;
      this.$store.dispatch("company/createCompany", this.model);
      this.$store.dispatch("company/readCompanyByUserId", this.usermodel);

      if (this.company.loading === false) {
        this.value = "successfull";
      }
    },
    uploadFile() {
      let input = this.$refs.file;
      console.log(input);
      let files = input.files;
      this.fileName = files[0].name;
      console.log(this.fileName);
      let size = files[0].size / 1000000;
      //console.log(size);
      if (size > 5) {
        swal(
          "Error",
          "File size must not be more than 5mb, please try with a reduced file size",
          "error"
        );
        return;
      }
      const reader = new FileReader();
      try {
        reader.onload = (e) => {
          this.base64 = e.target.result;
          console.log(e.target.result);
          this.uploadOfficerImage();
        };
        reader.readAsDataURL(files[0]);
        this.$emit("input", files[0]);
      } catch (e) {
        console.warn(e.message);
      }
    },
    async uploadOfficerImage() {
      this.showLoader = true;
      this.uploadmodel.username = `${
        this.auth.userInfo.userFirstName + this.auth.userInfo.userLastName
      }_${Date.now()}`;
      this.uploadmodel.base64 = this.base64;
      await this.$store.dispatch(
        "sbucket/uploadEncodedFile",
        this.uploadmodel,
        { root: true }
      );
      this.url = this.sbucket.s3bucketResponse.url;
      console.log(this.url);
      this.showLoader = false;
    },
  },
  computed: {
    ...mapState(["auth", "sbucket", "company", "campaign"]),
    createdCampaign: (state) => state.campaign.created,
    updatedCampaign: (state) => state.campaign.updated,
    viewcompany: (state) => state.company.company
  },
  watch: {
    timer() {
      console.log("timer has changed");
    },
    createdCampaign() {
      console.log("The campaign was created successfully");
      if (this.createdCampaign) {
        this.displayDescription();
      }
    },
    updatedCampaign() {
      if (this.updatedCampaign) {
        this.RouterUtils.changeRouteTo(
          this.RouterUtils.routes.company.ADD_CAMPAIGN_DETAILS.name
        );
      }
    },
  },
};
</script>

