<template>
  <home-layout>
    <div class="qoo-auth-section wf-section">
      <div class="qoo-auth-container">
        <a href="../index.html" class="w-inline-block"><img src="../../assets/img/Group-28.png" loading="lazy" width="150" alt="" class="image-3 sign-up"></a>
        <h1 class="auth-heading1-2">Reset Password</h1>
        <div class="auth-text-3 sub-heading">Enter your email and we will send you a link to <br>reset your password</div>
        <div class="auth-form w-form">
          <form id="email-form" name="email-form" method="get" @submit.prevent="sendOTP">
            <div class="form-field-container"><input type="email" class="form-field sign-up w-input" maxlength="256" name="field-2" data-name="Field 2" placeholder="Email Address" id="field-2" required="" v-model="model.username"></div>
            <button type="submit" class="auth-button reset-password w-button" v-if="!auth.authLoading">
              Continue
            </button>
            <div class="loader" v-else></div>

          </form>
          <div class="w-form-done">
            <div>Thank you! Your submission has been received!</div>
          </div>
          <div class="w-form-fail">
            <div>Oops! Something went wrong while submitting the form.</div>
          </div>
        </div>
      </div>
    </div>
  </home-layout>
</template>

<script>
import HomeLayout from "@/views/layout/HomeLayout";
import RouterUtils from "@/utils/BaseUtils/RouterUtils";
import AuthRequest from "@/models/requests/AuthRequest";
import {mapState} from "vuex";
export default {
  name: "ForgetPasswordPage",
  components: {HomeLayout},
  data(){
    return {
      RouterUtils: RouterUtils,
      model: AuthRequest.initiateResetPassword
    }
  },
  methods: {
    sendOTP(){
      localStorage.setItem("email", this.model.username)
      this.$store.dispatch("auth/initiateResetPassword", this.model)
    }
  },
  computed:{
    ...mapState(["auth"])
  }
}
</script>

