import client from "./BaseService";

export default {
    callCreateApi(payload) {
        return client.apiClient.post("/user/create", payload);
    },
    callReadApi(payload) {
        return client.apiClient.post("/user/read", payload);
    },
    callReadByIdApi(payload) {
        return client.apiClient.post("/user/read-by-user-id", payload);
    },
    callUpdateApi(payload){
        return client.apiClient.post("/user/update", payload);
    },

}