<template>
  <div>
    <TransitionRoot as="template" :show="open" onload="this.value1(this.changeValue)">
      <Dialog as="div" class="relative z-10" >
        <TransitionChild as="template" enter="ease-out duration-300" enter-from="opacity-0" enter-to="opacity-100" leave="ease-in duration-200" leave-from="opacity-100" leave-to="opacity-0">
          <div class="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
        </TransitionChild>

        <div class="fixed z-10 inset-0 overflow-y-auto" data-backdrop="static" >
          <div class="flex items-end sm:items-center justify-center min-h-full p-4 text-center sm:p-0" data-backdrop="static">
            <TransitionChild as="template" enter="ease-out duration-300" enter-from="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95" enter-to="opacity-100 translate-y-0 sm:scale-100" leave="ease-in duration-200" leave-from="opacity-100 translate-y-0 sm:scale-100" leave-to="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95" data-backdrop="static">
              <DialogPanel class="relative dark-blue rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:max-w-lg sm:w-full" data-backdrop="static">
                <div class="dark-blue px-4 pt-5 pb-4 sm:p-6 sm:pb-4" data-backdrop="static">
                  <div class="sm:flex modal-flex sm:items-start">
                    <div class="flex-shrink-0 flex items-center justify-center h-12 w-12 rounded-full bg-red-100 sm:mx-0 sm:h-10 sm:w-10 modal-move">
                      <ExclamationIcon class="h-6 w-6 text-red-600" aria-hidden="true" />
                    </div>
                    <div class="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                      <DialogTitle as="h3" class="my-text leading-6 font-medium text-gray-900">Time Up</DialogTitle>
                      <div class="mt-2">
                        <p class="text-small text-gray-500">Oh oh! You have ran out of time.</p>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="dark-blue px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
                  <button type="button" class="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-red-600 text-base font-medium text-white hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 sm:ml-3 sm:w-auto sm:text-sm" @click="open = false" style="border: none !important; width: 130px !important;" v-on:click= "this.RouterUtils.changeRouteTo(this.RouterUtils.routes.applicant.REVIEW.name);">Submit</button>
                </div>
              </DialogPanel>
            </TransitionChild>
          </div>
        </div>
      </Dialog>
    </TransitionRoot>
  </div>
</template>

<script>
import { ref } from 'vue';
import { Dialog, DialogPanel, DialogTitle, TransitionChild, TransitionRoot } from '@headlessui/vue';
import { ExclamationIcon } from '@heroicons/vue/outline';
import {mapState} from "vuex";
import RouterUtils from "../../utils/BaseUtils/RouterUtils";
export default {
  name: "TimeUpModal",
  components: {Dialog, DialogPanel, DialogTitle, TransitionChild, TransitionRoot, ExclamationIcon},
  data: () => ({
    open: ref(false),
    RouterUtils: RouterUtils
  }),
  props: {
    changeValue: Boolean
  },
  methods: {
    changeOpenValue(){
      this.$emit("hi", this.changeValue)
      console.log(this.changeValue)
      if (this.changeValue === true){
        this.open = ref(true)
      }
    }
  },
  mounted() {

    this.changeOpenValue();

  },
  watch: {
    changeValue: function (){
      if (this.changeValue === true){
        console.log(this.changeValue)
        this.open = ref(true)
      }
    }
  },
  computed:{
  ...mapState({
      instructions: state => state.instruction.instructions,
    })
  }
}
</script>

