<template>
  <home-layout>
    <div style="display: none; opacity: 1" class="qoo-modal paymentmodal">
      <div
        class="qoo-modal-body"
        style="display: block; width: 500px; height: 365px"
        v-if="userInfo.userVerificationStatus === 'VERIFIED'"
      >
        <a
          data-w-id="c35ae2e9-3f4d-1e05-587f-e30068a46e60"
          href="#"
          class="link-17"
          v-on:click="close('.paymentmodal')"
        ></a>
        <div class="walletmodalcontent">
          <div class="walletmodalheader">How much do you want to invest?</div>
          <div class="div-block-160">
            <div class="usdamounttext">USD</div>
            <div class="form-block-2 w-form">
              <form
                id="email-form-2"
                name="email-form-2"
                data-name="Email Form 2"
                method="get"
              >
                <input
                  type="number"
                  class="text-field-22 w-input"
                  id="amount"
                  maxlength="256"
                  name="Amount"
                  data-name="Amount"
                  placeholder="Enter Amount"
                  required="true"
                />
              </form>
              <div class="w-form-done">
                <div>Thank you! Your submission has been received!</div>
              </div>
              <div class="w-form-fail">
                <div>Oops! Something went wrong while submitting the form.</div>
              </div>
            </div>
          </div>
          <div class="usdamountitalic">
            Minimum amount is NGN 100.00. Card payment coming soon.
          </div>
          <button
            type="button"
            class="continuebutton w-button my-button"
            v-on:click="goToAgreement"
          >
            Continue
          </button>
          <div class="loader" style="display: none"></div>
        </div>
      </div>
      <div
        class="qoo-modal-body"
        v-else
        style="display: block; width: 450px; height: 365px; padding: 30px 45px"
      >
        <a
          data-w-id="c35ae2e9-3f4d-1e05-587f-e30068a46e60"
          href="#"
          class="link-17"
          v-on:click="close('.paymentmodal')"
          style="margin-right: -10px;margin-top: 5px;}"
        ></a>
        <div class="div-block-129">
          <img
            loading="lazy"
            src="../../assets/img/empty-state.svg"
            alt=""
            class="image-21"
          />
        </div>
        <div class="text-block-9" style="text-align: center">
          You have not verified your profile.
        </div>
        <button
          type="button"
          class="continuebutton w-button my-button"
          v-on:click="goToVerify"
          style="margin-top: 20px"
        >
          Verify Now
        </button>
      </div>
    </div>
    <AcknowledgementForm
      :display-modal="agreement"
      @show-success="finishPayment"
    />
    <div style="display: none; opacity: 1" class="qoo-modal successmodal">
      <div
        class="qoo-modal-body"
        style="display: block; width: 525px; height: 465px"
      >
        <a
          data-w-id="c35ae2e9-3f4d-1e05-587f-e30068a46e60"
          href="#"
          class="link-17"
          v-on:click="close('.successmodal')"
        ></a>
        <div class="walletmodalcontent">
          <lottie-player
            src="https://assets7.lottiefiles.com/datafiles/OhIfcxnkLsj1Jxj/data.json"
