              ><img
                src="../../assets/img/arrow-left.svg"
                loading="lazy"
                alt=""
                class="image-23"
              />
              <div class="text-block-23">Back to Explore</div>
            </a>
            <div class="details-wrapper">
              <div class="verified-company-name" style="width: 83%">
                <div class="company-logo" style="align-self: baseline">
                  <div
                    v-if="campaign.campaignCardImage"
                    :style="companyLogoStyle"
                    class="company-logo-image-div"
                  ></div>
                  <img
                    src="../../assets/img/Rectangle-655.png"
                    loading="lazy"
                    alt=""
                    v-else
                  />
                </div>
                <div class="company-name" style="width: 100%">
                  <div class="name">
                    <div class="text-block-24">
                      {{ campaign.data.campaignName }}
                    </div>
                    <img
                      src="../../assets/img/Group-33856.svg"
                      loading="lazy"
                      alt=""
                      class="image-22"
                    />
                  </div>
                  <div
                    style="
                      display: flex;
                      align-items: baseline;
                      margin-top: 10px;
                      padding: 2px 6px;
                    "
                    v-if="companyIndustry"
                  >
                    <div class="country-span">
                      <img
                        src="https://flagcdn.com/16x12/ca.png"
                        srcset="
                          https://flagcdn.com/32x24/ca.png 2x,
                          https://flagcdn.com/48x36/ca.png 3x
                        "
                        width="16"
                        height="12"
                        alt="Canada"
                      />
                      CA
                    </div>
                    <div
                      class="country field industry-name"
                      style="margin-left: 15px"
                      ref="industryName"
                    >
                      {{ companyUpload.data.companyUploadIndustry }}
                      <!--                      {{ companyUploadIndustry }}-->
                    </div>
                  </div>
                  <div class="no-preview-information" v-else>
                    <img
                      src="../../assets/img/bluecaution.svg"
                      loading="lazy"
                      alt=""
                      style="margin-right: 5px"
                    />
                    No information
                  </div>
                  <div
                    class="progress"
                    style="
                      height: 10px !important;
                      border-radius: 30px;
                      width: 100%;
                      margin-top: 20px;
                    "
                    @click="getEvent($event)"
                  >
                    <div
                      class="progress-bar"
                      style="background-color: #5dc9a0"
                      role="progressbar"
                      aria-valuenow="25"
                      aria-valuemin="0"
                      aria-valuemax="100"
                    ></div>
                  </div>
                  <div class="amount-text">
                    <p v-if="campaign.campaignAmount">
                      ${{ Number(campaign.campaignAmount).toLocaleString()
                      }}<span class="sub-text">Raised</span>
                    </p>
                    <p v-else>$0<span class="sub-text">Raised</span></p>
                    <p>0<span class="sub-text">Investors</span></p>
                    <p v-if="campaign.campaignTarget">
                      ${{ Number(campaign.campaignTarget).toLocaleString()
                      }}<span class="sub-text">Target</span>
                    </p>
                    <p v-else>$0<span class="sub-text">Target</span></p>
                    <p v-if="campaign.campaignDuration">
                      ${{ Number(campaign.campaignDuration).toLocaleString()
                      }}<span class="sub-text">Days Left</span>
                    </p>
                    <p v-else>0<span class="sub-text">Days Left</span></p>
                  </div>
                </div>
              </div>
