<script>
import HomeLayout from "@/views/layout/HomeLayout";
import RouterUtils from "@/utils/BaseUtils/RouterUtils";
import ProfileAccordion from "./ProfileAccordion.vue";
// import CountryField from "./CountryField.vue";
import CompanyRequest from "@/models/requests/CompanyRequest";
import EntryDisclaimerVue from "./EntryDisclaimer.vue";
import { mapState } from "vuex";
import ProjectionRequest from "@/models/requests/ProjectionRequest";
import TractionRequest from "@/models/requests/TractionRequest";
import CompanyUploadRequest from "@/models/requests/CompanyUploadRequest";
import AcknowledgementForm from "@/components/company/AcknowledgementForm";
import KeynoteRequest from "@/models/requests/KeynoteRequest";
// import LottiePlayer from 'lottie-player-vue';

export default {
  name: "CampaignPreviewPage",
  components: {
    AcknowledgementForm,
    HomeLayout,
    ProfileAccordion,
    EntryDisclaimerVue,
    // CountryField,
  },
  data() {
    return {
      projectionRevenue: false,
      RouterUtils: RouterUtils,
      settings: true,
      portfolio: false,
      wallet: false,
      formM: false,
      keynotemodel: KeynoteRequest.readById,
      companymodel: CompanyRequest.readById,
      companyUploadModel: CompanyUploadRequest.readByCompanyId,
      projectionread: ProjectionRequest.readById,
      tractionread: TractionRequest.readById,
      companyLogo: false,
      companyIndustry: false,
      companyDescription: false,
      routing: false,
      agreement: false,
      publicPath: process.env.BASE_URL,
    };
  },
  methods: {
    goToVerify() {
      this.RouterUtils.changeRouteTo(
        this.RouterUtils.routes.company.SETTINGS.name
      );
    },
    goToAgreement() {
      if (document.getElementById("amount").value !== "") {
        this.agreement = true;
        this.close(".paymentmodal");
      }
    },
    getEvent(event) {
      console.log(event);
    },
    goToSettings() {
      this.settings = true;
      this.portfolio = false;
      this.wallet = false;
      this.formM = false;
      this.wishlist = false;
    },
    goToPortfolio() {
      this.settings = false;
      this.portfolio = true;
      this.wallet = false;
      this.formM = false;
      this.wishlist = false;
    },
    goToWallet() {
      this.settings = false;
      this.portfolio = false;
      this.wallet = true;
      this.formM = false;
      this.wishlist = false;
    },
    goToFormM() {
      this.settings = false;
      this.portfolio = false;
      this.wallet = false;
      this.wishlist = false;
      this.formM = true;
    },
    goBackToDashboard() {
      this.RouterUtils.changeRouteTo(
        this.RouterUtils.routes.company.DASHBOARD.name
      );
    },
    goBackToExplore() {
      this.RouterUtils.changeRouteTo(
        this.RouterUtils.routes.company.EXPLORE.name
      );
    },
    openCity(evt, cityName, id) {
      // Declare all variables
      let i, tabcontent, tablinks;

      // Get all elements with class="tabcontent" and hide them
      tabcontent = document.getElementsByClassName("content");
      for (i = 0; i < tabcontent.length; i++) {
        document.getElementsByClassName("content")[i].style.display = "none";
      }

      // Get all elements with class="tablinks" and remove the class "active"
      tablinks = document.getElementsByClassName("tab");
      for (i = 0; i < tablinks.length; i++) {
        document.getElementsByClassName("tab")[i].className = tablinks[
          i
        ].className.replace(" w--current", "");
      }
      // Show the current tab, and add an "active" class to the button that opened the tab
      document.getElementById(cityName).style.display = "block";
      // document.getElementById(dataset.id).)
      document.getElementById(id).className += " w--current";
    },
    checkCompanyUpload() {
      if (this.companyUpload.data.companyUploadDescription !== null) {
        this.companyDescription = true;
      }
      if (this.companyUpload.data.companyUploadIndustry !== null) {
        this.companyIndustry = true;
      }
      if (this.company.data.companyLogo) {
        this.companyLogo = true;
      }
    },
    close(className) {
      document.querySelector(className).style.display = "none";
      this.awaitingUpload = true;
    },
    uploadProfile(className) {
      document.querySelector(className).style.display = "flex";
    },
    finishPayment(msg) {
      this.agreement = false;
      this.uploadProfile(msg);
    },
    // checkRoute(){
    //   if (this.$router.options.history.state.back === '/user/explore' || this.$router.options.history.state.back === '/explore'){
    //     this.routing = false
    //   }
    // }
  },
  mounted() {
    console.log("this is where i start");
    // this.checkRoute()
    console.log(this.$router);
    // console.log(this.$router.options.history.state.back)
    this.checkCompanyUpload();
  },
  computed: {
    ...mapState({
      company: (state) => state.company.company,
      campaign: (state) => state.campaign.campaign,
      companyUpload: (state) => state.companyUpload.companyUpload,
      keynote: (state) => state.keynote.keynote,
      projection: (state) => state.projection.projection,
      userInfo: (state) => state.auth.userInfo,
      // competition: state => state.competiton.competition,
    }),
    companyLogoStyle() {
      let output = {};
      output.backgroundImage = `url('${this.campaign.data.campaignCardImage}')`;
      return output;
    },
    // formMLink() {
    //   return require("@/assets/filler.pdf");
    // },
    // companyUploadIndustry() {
    //   let output = "Unknown";
    //   try {
    //     if (this.companyUpload.data[0].companyUploadIndustry) {
    //       output = this.companyUpload.data[0].companyUploadIndustry;
    //     }
    //   } catch (error) {
    //     error;
    //   }
    //   return output;
    // },
    heroPageDivStyle() {
      let output = {
        background: `rgba(0, 50, 92, 1)`,
      };
      try {
        if (this.company.data.companyBannerImage) {
          output.backgroundImage = `url('${this.company.data.companyBannerImage}')`;
        }
      } catch (error) {
        error;
      }
      return output;
    },
    companyNameString() {
      let output = "Company Name Unknown";
      try {
        if (this.company.data.companyName) {
          output = this.company.data.companyName;
        }
      } catch (error) {
        error;
      }
      return output;
    },
    companyUploadDescription() {
      let output = "Company Description Unknown";
      try {
        if (this.companyUpload.data.companyUploadDescription) {
          output = this.companyUpload.data.companyUploadDescription;
        }
      } catch (error) {
        error;
      }
      return output;
    },
    keynoteList() {
      let output = [
        {
          companyKeynote: "No Company Keynotes",
        },
      ];
      try {
        if (this.keynote.data) {
          output = this.keynote.data;
        }
      } catch (error) {
        error;
      }
      return output;
    },
  },
  watch: {
    campaign() {
      console.log(this.campaign);
      console.log("this is tthe reason");
      if (document.querySelector(".progress-bar") !== null) {
        document.querySelector(".progress-bar").ariaValueMax =
          this.campaign.data.campaignTarget;
        document.querySelector(".progress-bar").style.width =
          this.campaign.data.campaignAmount + "%";
      }
      this.companymodel.companyId = this.campaign.data.campaignCompanyId;
      this.$store.dispatch("company/readCompanyById", this.companymodel);
      console.log(this.company);
      console.log(this.companyUpload);
      this.companyUploadModel.companyUploadCompanyId =
        this.campaign.data.campaignCompanyId;
      this.keynotemodel.companyKeyCompanyId =
        this.campaign.data.campaignCompanyId;
      this.projectionread.projectionCompanyId =
        this.campaign.data.campaignCompanyId;
      this.tractionread.tractionCompanyId =
        this.campaign.data.campaignCompanyId;
      this.$store.dispatch(
        "projection/readProjectionById",
        this.projectionread
      );
      this.$store.dispatch("keynote/readKeynoteById", this.keynotemodel);
      this.$store.dispatch("traction/readTractionById", this.tractionread);
      this.$store.dispatch(
        "companyUpload/readByCompanyId",
        this.companyUploadModel
      );
      if (this.campaign.data.campaignCardImage) {
        this.companyLogo = true;
      }
    },
    companyUpload() {
      console.log(this.companyUpload);
      if (this.companyUpload.data.companyUploadIndustry) {
        this.companyIndustry = true;
      }
      if (this.companyUpload.data.companyUploadDescription) {
        this.companyDescription = true;
      }
