import client from "./BaseService";

export default {
    callCreateApi(payload) {
        return client.apiClient.post("/company-upload/create", payload);
    },
    callReadApi(payload) {
        return client.apiClient.post("/company-upload/read", payload);
    },
    callReadByCompanyIdApi(payload) {
        return client.apiClient.post("/company-upload/read-by-company-id", payload);
    },
    callUpdateApi(payload){
        return client.apiClient.post("/company-upload/update", payload);
    },
    callDeleteApi(payload){
        return client.apiClient.post("/company-upload/delete", payload);
    }

}