import TractionResponse from "@/models/response/TractionResponse";
import TractionService from "@/services/TractionService";
import TractionRequest from "@/models/requests/TractionRequest";
import BaseNotification from "@/components/baseNotification/BaseNotification";

export const state = {
  loading: false,
  traction: TractionResponse.readById,
  tractions: TractionResponse.read,
};

export const getters = {};

export const mutations = {
  updateLoading: (state, payload) => {
    state.loading = payload;
  },
  updateUserTraction: (state, payload) => {
    state.traction = payload;
  },
  updateStories: (state, payload) => {
    state.tractions = payload;
  },
};

export const actions = {
  createTraction({ commit }, payload = TractionRequest.create) {
    commit("updateLoading", true);
    return TractionService.callCreateApi(payload)
      .then((response) => {
        commit("updateLoading", false);
        let responseData = response.data;
        if (responseData.responseCode === "00") {
          BaseNotification.fireToast(
            "success",
            responseData.responseMessage
          ).then();
          commit("updateScreen", "success");
        } else {
          BaseNotification.fireToast(
            "error",
            responseData.responseMessage
          ).then();
        }
      })
      .catch((err) => {
        commit("updateLoading", false);
        console.error(err);
      });
  },
  updateTraction: ({ commit }, payload = TractionRequest.read) => {
    commit("updateLoading", true);
    return TractionService.callUpdateApi(payload)
      .then((response) => {
        let responseData = response.data;
        if (responseData.responseCode === "00") {
          commit("updateLoading", false);
          commit("updateInstruction", responseData);
          // BaseNotification.fireToast("success", responseData.responseMessage).then()
        } else {
          commit("updateLoading", false);
          BaseNotification.fireToast(
            "error",
            responseData.responseMessage
          ).then();
        }
      })
      .catch((error) => {
        commit("updateLoading", false);
        BaseNotification.fireToast("success", error).then();
      });
  },
  readTraction({ commit }, payload = TractionRequest.read) {
    return TractionService.callReadApi(payload).then((response) => {
      let responseData = response.data;
      if (responseData.responseCode === "00") {
        commit("updateAddedInstruction", responseData);
      }
    });
  },
  readTractionById({ commit }, payload = TractionRequest.readById) {
    return TractionService.callReadByIdApi(payload).then((response) => {
      let responseData = response.data;
      if (responseData.responseCode === "00") {
        commit("updateUserTraction", responseData);
        console.log(state.traction);
        // RouterUtils.changeRouteTo(RouterUtils.routes.traction.DASHBOARD.name)
      }
    });
  },
  deleteTraction() {},
};
