import {apiClient} from "./BaseService";

export default {
    callCreateApi(payload) {
        return apiClient.appClient.post("/test/create", payload);
    },
    callReadApi(payload) {
        return apiClient.appClient.post("/test/read", payload);
    },
    callUpdateApi(payload){
        return apiClient.appClient.post("/test/update", payload);
    },
    callDeleteApi(payload){
        return apiClient.appClient.post("/test/delete", payload);
    }

}