import CompetitionResponse from "@/models/response/CompetitionResponse";
import CompetitionService from "@/services/CompetitionService";
import CompetitionRequest from "@/models/requests/CompetitionRequest";
import BaseNotification from "@/components/baseNotification/BaseNotification";

export const state = {
    loading: false,
    competition: CompetitionResponse.readById,
    competitions: CompetitionResponse.read,

}

export const getters = {

}

export const mutations = {
    updateLoading: (state, payload) => {
        state.loading = payload;
    },
    updateCompetition: (state, payload) => {
        state.competition = payload
    },
    updateStories: (state, payload) => {
        state.competitions = payload;
    },
}

export const actions = {
    createCompetition({commit},payload = CompetitionRequest.create){
        commit("updateLoading", true)
        return CompetitionService.callCreateApi(payload)
            .then(response => {
                commit("updateLoading", false)
                let responseData = response.data
                if(responseData.responseCode === "00"){
                    BaseNotification.fireToast("success", responseData.responseMessage).then()
                    commit('updateScreen', 'success')
                }else{
                    BaseNotification.fireToast("error", responseData.responseMessage).then()
                }
            })
            .catch(err=>{
                commit("updateLoading", false)
                console.error(err)
            })
    },
    updateCompetition: ({ commit }, payload = CompetitionRequest.read) => {
        commit('updateLoading', true)
        return CompetitionService.callUpdateApi(payload).then(response => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateLoading", false)
                commit("updateInstruction", responseData)
                // BaseNotification.fireToast("success", responseData.responseMessage).then()

            } else {
                commit("updateLoading", false)
                BaseNotification.fireToast("error", responseData.responseMessage).then()
            }
        }).catch((error) => {
            commit("updateLoading", false)
            BaseNotification.fireToast("success", error).then()
        })

    },
    readCompetition({commit}, payload = CompetitionRequest.read) {
        return CompetitionService.callReadApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateAddedInstruction", responseData)
            }
        })
    },
    readCompetitionById({commit}, payload = CompetitionRequest.readById) {
        return CompetitionService.callReadByIdApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateCompetition", responseData)
                console.log(state.competition)
                // RouterUtils.changeRouteTo(RouterUtils.routes.competition.DASHBOARD.name)
            }
        })
    },
    deleteCompetition(){

    }
}
