<template>
    <div class="countdown" id="countdown">
      <div class="base-timer">
        <svg class="base-timer__svg" viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
          <g class="base-timer__circle">
            <circle class="base-timer__path-elapsed" cx="50" cy="50" r="45"></circle>
            <path
                id="base-timer-path-remaining"
                stroke-dasharray="283"
                class="base-timer__path-remaining"
                d="
          M 50, 50
          m -45, 0
          a 45,45 0 1,0 90,0
          a 45,45 0 1,0 -90,0
        "
            ></path>
          </g>
        </svg>
        <span id="base-timer-label" class="base-timer__label">{{this.formatTime(1200)}}</span>
      </div>
    </div>
</template>

<script>
export default {
  name: "TimerVue",
  components: {},
  emits: ["timeFinished", "timeBinished"],
  data() {
    return {
      array: 283,
      warningThreshold: 10,
      ALERT_THRESHOLD: 5,
      colour: {
        info: {
          color: "green"
        },
        warning: {
          color: "orange",
          threshold: this.warningThreshold
        },
        alert: {
          color: "red",
          threshold: this.ALERT_THRESHOLD
        }
      },
      timeLimit: 1200,
      timePassed: 0,
      timeLeft: this.timeLimit,
      timerInterval: null,
      remainingPathColor: "green",
      timeRemains: this.timeLeft
    }
  },

  methods: {
    onTimesUp() {
      clearInterval(this.timerInterval);
      this.$emit("timeBinished", this.timeLeft)
    },
    startTimer() {
      document.getElementById('base-timer-path-remaining').classList.add(this.remainingPathColor)
      this.timerInterval = setInterval(() => {
        this.timePassed = this.timePassed += 1;
        this.timeLeft = this.timeLimit - this.timePassed;
        document.getElementById("base-timer-label").innerHTML = this.formatTime(this.timeLeft);
        this.setCircleDasharray();
        this.setRemainingPathColor(this.timeLeft);
        localStorage.setItem("time-left", this.timeLeft)
        if (this.timeLeft === 0) {
          this.onTimesUp();
          this.$emit("timeFinished")
        // container.style.filter = "blur(8px)";
        // $("#timeUp").modal();
        // stopCapture();
        // stopRecording();
          }
        }, 1000);
  },
    formatTime(time) {
      const minutes = Math.floor(time / 60);
      let seconds = time % 60;

      if (seconds < 10) {
        seconds = `0${seconds}`;
      }
      return `${minutes}:${seconds}`;
  },
    setRemainingPathColor(timeLeft) {
      const { alert, warning, info } = this.colour;
      if (timeLeft <= alert.threshold) {
      document.getElementById("base-timer-path-remaining").classList.remove(warning.color);
      document.getElementById("base-timer-path-remaining").classList.add(alert.color);
      } else if (timeLeft <= warning.threshold) {
      document.getElementById("base-timer-path-remaining").classList.remove(info.color);
      document.getElementById("base-timer-path-remaining").classList.add(warning.color);
      }
    },
    calculateTimeFraction() {
      const rawTimeFraction = this.timeLeft / this.timeLimit;
      return rawTimeFraction - (1 / this.timeLimit) * (1 - rawTimeFraction);
    },
    setCircleDasharray() {
      const circleDasharray = `${(
      this.calculateTimeFraction() * this.array
      ).toFixed(0)} 283`;
    document.getElementById("base-timer-path-remaining").setAttribute("stroke-dasharray", circleDasharray);
    },

  },
  mounted() {
    this.startTimer()
  }
}

</script>

