import UserResponse from "@/models/response/UserResponse";
import UserService from "@/services/UserService";
import UserRequest from "@/models/requests/UserRequest";
import BaseNotification from "@/components/baseNotification/BaseNotification";

export const state = {
    loading: false,
    user: UserResponse.readById,
    users: UserResponse.read,

}

export const getters = {

}

export const mutations = {
    updateLoading: (state, payload) => {
        state.loading = payload;
    },
    updateUser: (state, payload) => {
        state.user = payload
    },
    updateUsers: (state, payload) => {
        state.users = payload;
    },
}

export const actions = {
    createUser({commit},payload = UserRequest.create){
        commit("updateLoading", true)
        return UserService.callCreateApi(payload)
            .then(response => {
                commit("updateLoading", false)
                let responseData = response.data
                if(responseData.responseCode === "00"){
                    BaseNotification.fireToast("success", responseData.responseMessage).then()
                    commit('updateScreen', 'success')
                }else{
                    BaseNotification.fireToast("error", responseData.responseMessage).then()
                }
            })
            .catch(err=>{
                commit("updateLoading", false)
                console.error(err)
            })
    },
    updateUser: ({ commit }, payload = UserRequest.read) => {
        commit('updateLoading', true)
        return UserService.callUpdateApi(payload).then(response => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateLoading", false)
                commit("updateInstruction", responseData)
                BaseNotification.fireToast("success", responseData.responseMessage).then()

            } else {
                commit("updateLoading", false)
                BaseNotification.fireToast("error", responseData.responseMessage).then()
            }
        }).catch((error) => {
            commit("updateLoading", false)
            BaseNotification.fireToast("success", error).then()
        })

    },
    readUser({commit}, payload = UserRequest.read) {
        return UserService.callReadApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateAddedInstruction", responseData)
            }
        })
    },
    readUserById({commit}, payload = UserRequest.readById) {
        return UserService.callReadByIdApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                console.log(responseData)
                commit("updateUser", responseData)
                console.log(state.user)
                // RouterUtils.changeRouteTo(RouterUtils.routes.key.DASHBOARD.name)
            }
        })
    },
    deleteUser(){

    }
}
