  <div
    class="active-campaigns"
    @click="goToUpdate"
    :class="{
      verified: campaign.campaign.data.campaignStatus != 'PENDING',
    }"
  >
    <h4>Active Campaigns</h4>

    <table class="detail-table">
      <tr>
        <th class="">Campaign Name:</th>
        <td>{{ campaign.campaign.data.campaignName }}</td>
      </tr>

      <tr>
        <th class="">Amount:</th>
        <td>
          {{ amountFormat }}
        </td>
      </tr>

      <tr>
        <th class="">Status:</th>
        <td>{{ campaign.campaign.data.campaignStatus }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
import { mapState } from "vuex";
import CampaignRequest from "@/models/requests/CampaignRequest";
import RouterUtils from "@/utils/BaseUtils/RouterUtils";

const formatter = new Intl.NumberFormat("en-US", {
  style: "currency",
  currency: "CAD",

  // These options are needed to round to whole numbers if that's what you want.
  //minimumFractionDigits: 0, // (this suffices for whole numbers, but will print 2500.10 as $2,500.1)
  //maximumFractionDigits: 0, // (causes 2500.99 to be printed as $2,501)
});

export default {
  name: "TableComponent",
  data() {
    return {
      customData: {
        name: "SEENI AND NIFEMI",
        amount: 50000,
        status: "verified",
        model: CampaignRequest.readByCompanyId,
      },
    };
  },
  computed: {
    ...mapState(["company", "campaign"]),
    practiceSets() {
      let data = [];
      const temp = this.customData;
      console.log(temp.amount);
      let tempAmount = temp.amount;
      console.log("tempamount", tempAmount);
      temp.amount = formatter.format(tempAmount);
      for (let i = 0; i < 4; i++) {
        data.push(temp);
      }
      console.log(data);
      return data;
    },
    amountFormat() {
      let output = "";
      let value = this.campaign.campaign.data.campaignTarget;
      output = formatter.format(value).replace(/(\.|,)00$/g, "");
      return output;
    },
  },
  mounted() {
    this.model.campaignCompanyId = this.company.company.data.companyId;
    this.$store.dispatch("campaign/readCampaignByCompanyId", this.model);
  },
  methods: {
    goToUpdate() {
      let campaignComplete = Boolean(
        this.campaign.campaign.data.campaignOverview &&
          this.campaign.campaign.data.campaignProblem &&
          this.campaign.campaign.data.campaignSolution &&
          this.campaign.campaign.data.campaignUniqueness
      );

      if (!campaignComplete) {
        RouterUtils.changeRouteTo(RouterUtils.routes.company.ADD_CAMPAIGN.name);
        // console.log(RouterUtils.routes.company.ADD_CAMPAIGN);
      } else if (
        this.campaign.campaign.data.campaignStatus != "PENDING" &&
        this.campaign.campaign.data.campaignVideo != null
      ) {
        RouterUtils.changeRouteTo(
          RouterUtils.routes.company.CAMPAIGN_PREVIEW.name
        );
      } else {
        RouterUtils.changeRouteTo(
          RouterUtils.routes.company.ADD_CAMPAIGN_DETAILS.name
        );
      }
    },
  },
};
</script>

