                maxlength="1" name="field-2" data-name="Field 2" placeholder="" id="field-2" required=""><input
                type="text" class="form-field email-verify w-input" maxlength="1" name="field-2" data-name="Field 2"
                placeholder="" id="field-2" required=""></div> -->
            <div
                style="
                display: flex;
                flex-direction: row;
                justify-content: center;
                margin-bottom: 30px;
              "
            >
              <v-otp-input
                  ref="otpInput"
                  input-classes="otp-input"
                  separator="<span style='opacity: 0;'> - </span>"
                  :num-inputs="6"
                  :should-auto-focus="true"
                  :is-input-num="true"
                  :conditionalClass="[
                  'one',
                  'two',
                  'three',
                  'four',
                  'five',
                  'six',
                ]"
                  :placeholder="['*', '*', '*', '*', '*', '*']"
                  @on-change="(otp) => handleOnChange(otp)"
                  @on-complete="(otp) => handleOnComplete(otp)"
              />
            </div>

            <div></div>

            <div class="form-field-container">
              <input
                  type="password"
                  class="form-field sign-up w-input"
                  maxlength="256"
                  name="field-2"
                  data-name="Field 2"
                  placeholder="Password (min. of 6 characters)"
                  id="field-2"
                  required=""
                  v-model="model.password"
              />
            </div>

            <div :class="['form-field-container']">
              <input
                  type="password"
                  class="form-field sign-up w-input"
