                  id="field-2"
                  required=""
                  v-model="model.confirmPassword"
              />
            </div>

            <div class="auth-text-4">
              Didn't receive otp?
              <a class="auth-text-4 link resend-otp" @click="resendOtp"
              >Resend</a
              >
            </div>
            <div
