import WalletResponse from "@/models/response/WalletResponse";
import WalletService from "@/services/WalletService";
import WalletRequest from "@/models/requests/WalletRequest";
import BaseNotification from "@/components/baseNotification/BaseNotification";

export const state = {
    loading: false,
    wallet: WalletResponse.create,
    wallets: WalletResponse.read,
    balance: WalletResponse.readBalance,
    transactions: WalletResponse.readTransaction
}

export const getters = {

}

export const mutations = {
    updateLoading: (state, payload) => {
        state.loading = payload;
    },
    updateWallet: (state, payload) => {
        state.wallet = payload
    },
    updateWallets: (state, payload) => {
        state.wallets = payload;
    },
    updateBalance: (state, payload) => {
        state.balance = payload;
    },
    updateTransactions: (state, payload) => {
        state.transactions = payload;
    }
}

export const actions = {
    createWallet({commit},payload = WalletRequest.create){
        commit("updateLoading", true)
        return WalletService.callCreateApi(payload)
            .then(response => {
                commit("updateLoading", false)
                let responseData = response.data
                if(responseData.client_secret !== ''){
                    commit("updateWallet", responseData)
                    console.log(responseData)
                    BaseNotification.fireToast("success", responseData.responseMessage).then()
                }else{
                    BaseNotification.fireToast("error", responseData.responseMessage).then()
                }
            })
            .catch(err=>{
                commit("updateLoading", false)
                console.error(err)
            })
    },

    editWallet: ({ commit }, payload = WalletRequest.read) => {
        commit('updateLoading', true)
        return WalletService.callUpdateApi(payload).then(response => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateLoading", false)
                commit("updateWallet", responseData)
                // BaseNotification.fireToast("success", responseData.responseMessage).then()

            } else {
                commit("updateLoading", false)
                BaseNotification.fireToast("error", responseData.responseMessage).then()
            }
        }).catch((error) => {
            commit("updateLoading", false)
            BaseNotification.fireToast("success", error).then()


        })

    },
    readBalance({commit}, payload = WalletRequest.readBalance) {
        return WalletService.callReadBalanceApi(payload).then((response) => {
            console.log(response)
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateBalance", responseData)
            }
        })
    },
    readTransactions({commit}, payload = WalletRequest.readTransaction) {
        return WalletService.callReadTransactioinApi(payload).then((response) => {
            console.log(response)
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateTransactions", responseData)
            }
        })
    },
    deleteWallet(){

    }
}
