import CompanyResponse from "@/models/response/CompanyResponse";
import CompanyService from "@/services/CompanyService";
import CompanyRequest from "@/models/requests/CompanyRequest";
import BaseNotification from "@/components/baseNotification/BaseNotification";

export const state = {
    loading: false,
    company: CompanyResponse.readById,
    companys: CompanyResponse.read,
    timer: false,
    userCompany: CompanyResponse.readByUserId,
    createdCompany: CompanyResponse.create.data,
    settings: 'settings'
}

export const getters = {

}

export const mutations = {
    updateLoading: (state, payload) => {
        state.loading = payload;
    },
    updateCompany: (state, payload) => {
        state.company = payload
    },
    updateCreatedCompany: (state, payload) => {
        state.createdCompany = payload
    },
    updateStories: (state, payload) => {
        state.companys = payload;
    },
    updateTimer: (state, payload) => {
        state.timer = payload;
    },
    updateUserCompany: (state, payload) => {
        state.userCompany = payload;
    },
    updateSettings: (state, payload) => {
        state.settings = payload;
    }
}

export const actions = {
    updateSettings({commit}, payload = {}){
        commit("updateSettings", 'wallet')
        console.log(payload)
        return true
    },
    createCompany({commit},payload = CompanyRequest.create){
        commit("updateLoading", true)
        return CompanyService.callCreateApi(payload)
            .then(response => {
                commit("updateLoading", false)
                let responseData = response.data
                if(responseData.responseCode === "00"){
                    commit("updateCompany", responseData)
                    BaseNotification.fireToast("success", responseData.responseMessage).then()
                    commit("updateTimer", true)
                }else{
                    commit("updateLoading", false)
                    BaseNotification.fireToast("error", responseData.responseMessage).then()
                }
            })
            .catch(err=>{
                commit("updateLoading", false)
                console.error(err)
            })
    },
    updateCompany: ({ commit }, payload = CompanyRequest.read) => {
        commit('updateLoading', true)
        return CompanyService.callUpdateApi(payload).then(response => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateLoading", false)
                commit("updateInstruction", responseData)
                BaseNotification.fireToast("success", responseData.responseMessage).then()

            } else {
                commit("updateLoading", false)
                BaseNotification.fireToast("error", responseData.responseMessage).then()
            }
        }).catch((error) => {
            commit("updateLoading", false)
            BaseNotification.fireToast("success", error).then()
        })

    },
    readCompany({commit}, payload = CompanyRequest.read) {
        return CompanyService.callReadApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateLoading", false)
            }
            else {
                commit("updateLoading", false)
                BaseNotification.fireToast("error", responseData.responseMessage).then()
            }
        })
    },
    readCompanyById({commit}, payload = CompanyRequest.readById) {
        return CompanyService.callReadByIdApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateLoading", false)
                commit("updateCompany", responseData)
                console.log(state.company)
                // RouterUtils.changeRouteTo(RouterUtils.routes.company.DASHBOARD.name)
            }
            else {
                commit("updateLoading", false)
                localStorage.setItem("id", responseData.data.companyId)
                BaseNotification.fireToast("error", responseData.responseMessage).then()
            }
        })
    },
    readByVerificationStatus({commit}, payload = CompanyRequest.readByVerificationStatus) {
        return CompanyService.callReadByVerificationStatus(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateStories", responseData)
                console.log(state.company)
                // RouterUtils.changeRouteTo(RouterUtils.routes.company.DASHBOARD.name)
            }
            else {
                commit("updateLoading", false)
                localStorage.setItem("id", responseData.data.companyId)
                BaseNotification.fireToast("error", responseData.responseMessage).then()
            }
        })
    },
    getFormA({commit}, payload = CompanyRequest.readById) {
        return CompanyService.callFormM(payload).then((response) => {
            console.log(response)
            commit("updateMessage", response)
        })
    },
    readCompanyByUserId({commit}, payload = CompanyRequest.readByUserId) {
        return CompanyService.callReadByUserIdApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateLoading", false)
                commit("updateUserCompany", responseData.data)
                console.log(state.company)
                // RouterUtils.changeRouteTo(RouterUtils.routes.company.DASHBOARD.name)
            }
            else {
                commit("updateLoading", false)
                BaseNotification.fireToast("error", responseData.responseMessage).then()
            }
        })
    },
    deleteCompany(){

    }
}
