<template>
  <div class="settings-wrapper">
    <div class="main-content">
      <div class="wallet-top-details">
        <div class="balance-wrapper">
          <div class="balance-details">
            <div class="total-balance-text-2">Total Balance<br></div>
            <div class="amount"><span class="text-span-6">${{getBeforeValue(this.balance.accountBalance)}}</span>.{{getAfterValue(this.balance.accountBalance)}}</div>
          </div>
          <a data-w-id="f8a78887-a0a5-d2e8-909f-4727c1522d37" href="#" class="qoo-button-filled w-button" v-on:click="topUp">Top up</a>
        </div>
        <div v-if="walletEmpty">
          <div class="recent">
            <div class="recent-transactions">Recent transactions<br></div>
          </div>
          <div class="recent-transactions-wrapper-2">
            <div class="funding-details _1" v-for="(item, key) in transactions.data" :key="key" >
              <div class="wallet-funded_details">
                <div class="funded">
                  <div class="funded_wallet">{{getTransactionType(item.transactionType)}}</div>
                  <div class="amount-funded">${{getBeforeValue(item.amount)}}.{{getAfterValue(item.amount)}}</div>
                </div>
                <div class="date-funded">
                  <div class="date_funded">{{item.transactionDate}}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="empty-state" v-else>
          <div class="div-block-129"><img loading="lazy" src="../../assets/img/empty-state.svg" alt="" class="image-21">
            <div class="text-block-103">Nothing to show here</div>
            <div class="text-block-21">You have no transaction. When you do, it'll show up here.</div>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import {mapState} from "vuex";
export default {
  name: "companyWallet",
  data(){
    return {
      walletEmpty: true
    }
  },
  computed:{
    ...mapState({
      balance: state => state.wallet.balance,
      transactions: state => state.wallet.transactions
    }),

  },
  methods: {
    getBeforeValue(value){
      return value.toString().split('.')[0]
    },
    getAfterValue(value){
      return value.toString().split('.')[1]
    },
    getTransactionType(value){
      if (value === "INWARD"){
        return "Credit"
      }
      else {
        document.querySelector('.funding-details._1').style.backgroundImage = "url('../../assets/img/Group-34145.svg')"
        return "Debit"
      }
    },
    close(){
      document.querySelector('.walletmodal').style.display = 'none';
    },
    topUp(){
      document.querySelector('.walletmodal').style.display = 'flex';
    },
  },
  mounted() {
    console.log(this.balance)
    console.log(this.transactions)
  },
  watch: {
    balance(){
      console.log(this.balance)
    },
    transactions(){
      console.log(this.transactions)
    }
  }
}
</script>

