import {apiClient} from "./BaseService";

export default {
    callCreateApi(payload) {
        return apiClient.appClient.post("/instruction/create", payload);
    },
    callReadApi(payload) {
        return apiClient.appClient.post("/instruction/read", payload);
    },
    callUpdateApi(payload){
        return apiClient.appClient.post("/instruction/update", payload);
    },
    callDeleteApi(payload){
        return apiClient.appClient.post("/instruction/delete", payload);
    }

}