import CompanyUploadResponse from "@/models/response/CompanyUploadResponse";
import CompanyUploadService from "@/services/CompanyUploadService";
import CompanyUploadRequest from "@/models/requests/CompanyUploadRequest";
import BaseNotification from "@/components/baseNotification/BaseNotification";

export const state = {
    loading: false,
    companyUpload: CompanyUploadResponse.readByCompanyId,
    companyUploaded: CompanyUploadResponse.companyUpload,
    companyUploads: CompanyUploadResponse.read,
    companyInfo: {}
}

export const getters = {
    getCompany: (state) => {
        // console.log("getter getTok")
        return state.companyInfo;
    },
}

export const mutations = {
    updateLoading: (state, payload) => {
        state.loading = payload;
    },
    updateCompanyUpload: (state, payload) => {
        state.companyUpload = payload;
    },
    updatecompanyUploads: (state, payload) => {
        state.companyUploads = payload;
    },
    updateCompanyUploaded: (state, payload) => {
        state.companyUploaded = payload;
    }
}

export const actions = {
    createCompanyUpload({commit},payload = CompanyUploadRequest.create){
        commit("updateLoading", true)
        return CompanyUploadService.callCreateApi(payload)
            .then(response => {
                commit("updateLoading", false)
                let responseData = response.data
                if(responseData.responseCode === "00"){
                    BaseNotification.fireToast("success", responseData.responseMessage).then()
                    commit('updateScreen', 'success')
                    commit("updateCompanyInfo", responseData);
                    console.log(this.companyInfo)
                }else{
                    BaseNotification.fireToast("error", responseData.responseMessage).then()
                }
            })
            .catch(err=>{
                commit("updateLoading", false)
                console.error(err)
            })
    },
    updateCompanyUpload: ({ commit }, payload = CompanyUploadRequest.read) => {
        commit('updateLoading', true)
        return CompanyUploadService.callUpdateApi(payload).then(response => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateLoading", false)
                commit("updateInstruction", responseData)
                // BaseNotification.fireToast("success", responseData.responseMessage).then()

            } else {
                commit("updateLoading", false)
                BaseNotification.fireToast("error", responseData.responseMessage).then()
            }
        }).catch((error) => {
            commit("updateLoading", false)
            BaseNotification.fireToast("success", error).then()
        })

    },
    readCompanyUpload({commit}, payload = CompanyUploadRequest.read) {
        return CompanyUploadService.callReadApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateCompanyUpload", responseData)
            }
        })
    },
    readByCompanyId({commit}, payload = CompanyUploadRequest.readByCompanyId) {
        return CompanyUploadService.callReadByCompanyIdApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateCompanyUpload", responseData)
                console.log(responseData)
            }
        })
    },
    deleteCompanyUpload(){

    }
}
