import CampaignResponse from "@/models/response/CampaignResponse";
import CampaignService from "@/services/CampaignService";
import CampaignRequest from "@/models/requests/CampaignRequest";
import BaseNotification from "@/components/baseNotification/BaseNotification";

export const state = {
  loading: false,
  campaign: CampaignResponse.readById,
  campaigns: CampaignResponse.read,
  timer: false,
  companyCampaign: CampaignResponse.readByCompanyId,
  createdCampaign: CampaignResponse.create.data,
  settings: "settings",
  created: false,
  updated: false,
};

export const getters = {};

export const mutations = {
  updateCreated: (state, payload) => {
    console.log("created");
    state.created = payload;
  },
  updateUpdated: (state, payload) => {
    state.updated = payload;
  },
  updateLoading: (state, payload) => {
    state.loading = payload;
  },
  updateCampaign: (state, payload) => {
    state.campaign = payload;
  },
  updateCreatedCampaign: (state, payload) => {
    state.createdCampaign = payload;
  },
  updateCampaigns: (state, payload) => {
    state.campaigns = payload;
  },
  updateTimer: (state, payload) => {
    state.timer = payload;
  },
  updateUserCampaign: (state, payload) => {
    state.userCampaign = payload;
  },
  updateSettings: (state, payload) => {
    state.settings = payload;
  },
};

export const actions = {
  updateSettings({ commit }, payload = {}) {
    commit("updateSettings", "wallet");
    console.log(payload);
    return true;
  },
  createCampaign({ commit }, payload = CampaignRequest.create) {
    commit("updateCreated", false);
    commit("updateLoading", true);
    return CampaignService.callCreateApi(payload)
      .then((response) => {
        commit("updateLoading", false);
        let responseData = response.data;
        if (responseData.responseCode === "00") {
          commit("updateCampaign", responseData);
          BaseNotification.fireToast(
            "success",
            responseData.responseMessage
          ).then();
          commit("updateCreated", true);
        } else {
          BaseNotification.fireToast(
            "error",
            responseData.responseMessage
          ).then();
        }
      })
      .catch((err) => {
        commit("updateLoading", false);
        console.error(err);
      });
  },
  updateCampaign: ({ commit }, payload = CampaignRequest.read) => {
    commit("updateUpdated", false);
    commit("updateLoading", true);
    return CampaignService.callUpdateApi(payload)
      .then((response) => {
        let responseData = response.data;
        if (responseData.responseCode === "00") {
          commit("updateLoading", false);
          commit("updateCampaign", responseData);
          BaseNotification.fireToast(
            "success",
            responseData.responseMessage
          ).then();
          commit("updateUpdated", true);
        } else {
          commit("updateLoading", false);
          BaseNotification.fireToast(
            "error",
            responseData.responseMessage
          ).then();
        }
      })
      .catch((error) => {
        commit("updateLoading", false);
        BaseNotification.fireToast("success", error).then();
      });
  },
  readCampaign({ commit }, payload = CampaignRequest.read) {
    commit("updateLoading", true);
    return CampaignService.callReadApi(payload).then((response) => {
      let responseData = response.data;
      if (responseData.responseCode === "00") {
        commit("updateLoading", false);
        commit("updateCampaigns", responseData);
      } else {
        commit("updateLoading", false);
      }
    });
  },
  readCampaignById({ commit }, payload = CampaignRequest.readById) {
    commit("updateLoading", true);
    return CampaignService.callReadByIdApi(payload).then((response) => {
      let responseData = response.data;
      if (responseData.responseCode === "00") {
        commit("updateLoading", false);
        commit("updateCampaign", responseData);
        console.log(state.campaign);
        // RouterUtils.changeRouteTo(RouterUtils.routes.campaign.DASHBOARD.name)
      } else {
        commit("updateLoading", false);
      }
    });
  },
  readCampaignByCompanyId(
    { commit },
    payload = CampaignRequest.readByCompanyId
  ) {
    return CampaignService.callReadByCompanyIdApi(payload).then((response) => {
      let responseData = response.data;
      if (responseData.responseCode === "00") {
        commit("updateLoading", false);
        commit("updateCampaign", responseData);
        console.log(state.campaign);
        // RouterUtils.changeRouteTo(RouterUtils.routes.campaign.DASHBOARD.name)
      } else {
        commit("updateLoading", false);
      }
    });
  },
  deleteCampaign() {},
};
