import client from "./BaseService";

export default {
    callCreateApi(payload) {
        return client.apiClient.post("/create-payment-intent", payload);
    },
    callReadApi(payload) {
        return client.apiClient.post("/wallet/read", payload);
    },
    callReadBalanceApi(payload) {
        return client.apiClient.post("/wallet/balance-enquiry", payload);
    },
    callReadTransactioinApi(payload) {
        return client.apiClient.post("/wallet/history", payload);
    },
    callUpdateApi(payload){
        return client.apiClient.post("/wallet/update", payload);
    },
    callDeleteApi(payload){
        return client.apiClient.post("/wallet/delete", payload);
    }

}


