<template>
  <home-layout>
    <div class="qoo-auth-section wf-section">
      <div class="qoo-auth-container">
        <a href="" class="w-inline-block"><img src="../../assets/img/Group-28.png" loading="lazy" width="150" alt="" class="image-3 sign-up"></a>
        <h1 class="auth-heading1-2">Log into your account </h1>
        <div class="auth-form w-form">
          <form id="email-form" name="email-form" data-name="Email Form" @submit.prevent="login">
            <div class="form-field-container"><input type="email" class="form-field sign-up w-input" maxlength="256" name="field-2" data-name="Field 2" placeholder="Email Address" id="field-2" required="" v-model="model.username"></div>
            <div class="form-field-container login"><input type="password" class="form-field sign-up w-input" maxlength="256" name="field-2" data-name="Field 2" placeholder="Password (min. of 6 characters)" id="field-3" required="" v-model="model.password"></div>
            <div class="form-field-container forgot--password">
              <a class="auth-text-2 link forgot-password" v-on:click="goToForgotPassword">Forgot Password ?</a>
            </div>
            <div class="auth-text">Don't have an account? <a href="#" class="auth-text-2 link" v-on:click="goToRegister">Sign Up</a>
            </div>
            <button v-if="!auth.authLoading" :disabled="disabled" type="submit" style="background: #63b8a0; border-color: #63b8a0;" class="form-control b-button">Login</button>
            <div class="loader" v-else></div>
<!--            <button type="submit">submit</button>-->
          </form>
          <div class="w-form-done">
            <div>Thank you! Your submission has been received!</div>
          </div>
          <div class="w-form-fail">
            <div>Oops! Something went wrong while submitting the form.</div>
          </div>
        </div>
      </div>
    </div>
  </home-layout>
</template>

<script>
import HomeLayout from "@/views/layout/HomeLayout";
import RouterUtils from "@/utils/BaseUtils/RouterUtils";
import AuthRequest from "@/models/requests/AuthRequest";
// import BaseButton from "@/components/button/BaseButton";
import {mapState} from "vuex";
export default {
  name: "LoginPage",
  components: {HomeLayout},
  data(){
    return {
      RouterUtils: RouterUtils,
      model: AuthRequest.login
    }
  },
  methods: {
    goToForgotPassword(){
      this.RouterUtils.changeRouteTo(this.RouterUtils.routes.company.FORGOT_PASSWORD.name)
    },
    goToRegister(){
      this.RouterUtils.changeRouteTo(this.RouterUtils.routes.company.REGISTER.name)
    },
    login() {
      console.log("hiiiii")
      localStorage.setItem('loginEmail', this.model.username)
      this.$store.dispatch("auth/login", this.model)
    },
  },
  computed:{
    ...mapState(["auth"])
  }
}
</script>

