<template>
  <div>
    <h1>Excel</h1>
    <input type="file" id="file" @change="uploadFile" ref="file" :disabled="disableInput" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel">
    <div v-for="(items, index) in this.items" :key="index">
      <!--      <p>{{items}}</p>-->
      <p>{{items2}}</p>
    </div>
  </div>
</template>

<script>
import readXlsxFile from 'read-excel-file'
import S3Request from "@/models/requests/S3Request";
import { mapState } from "vuex";
import swal from "sweetalert";
import CompanyUploadRequest from "@/models/requests/CompanyUploadRequest";
export default {
  name: "UploadFile",
  data() {
    return{
      items: [],
      url:"",
      disableInput: false,
      showLoader: false,
      fileName: "",
      base64: "",
      model: S3Request.prototype.uploadBase64(),
      items2: [],
      uploadModel: CompanyUploadRequest.create
    }
  },
  methods: {
    subirExcel(){
      const file = document.getElementById('file');
      readXlsxFile(file.files[0]).then((rows) => {
        this.items = rows
        console.log(this.items)
      })
    },
    uploadFile(){
      const file = document.getElementById('file');
      readXlsxFile(file.files[0]).then((rows) => {
        this.items = rows
        // console.log(this.items)
      })
      readXlsxFile(file.files[0], { sheet: 'Company' }).then((data) => {
        this.items2 = data;
        console.log(this.items2[0][3])
        console.log(this.items2[1][3])
        this.uploadModel.companyUploadDescription = this.items2[1][3]
        this.uploadModel.companyUploadExternalAuditor = this.items2[4][3]
        console.log(this.items2[3][3])
        this.uploadModel.companyUploadMarketSize = this.items2[3][3]
        console.log(this.items2[4][3])
        this.uploadModel.companyUploadMission = this.items2[5][3]
        console.log(this.items2[5][3])
        this.uploadModel.companyUploadIncorporated = this.items2[6][3]
        this.uploadModel.companyUploadIndustry = this.items2[10][3]

        this.uploadModel.companyUploadIncorportedDate = this.items2[7][3]
        this.uploadModel.companyUploadIncorporatedCountry = this.items2[8][3]
        console.log(this.items2[6][3])
        this.uploadModel.companyUploadIncorporationType = this.items2[9][3]
        console.log(this.items2[7][3])
        this.uploadModel.companyUploadIncorporationIndustry = this.items2[10][3]
        console.log(this.items2[8][3])
        this.uploadModel.companyUploadNumberOfStaff = this.items2[11][3]
        console.log(this.items2[9][3])
        // this.items2[9][3] = this.uploadModel.z
        console.log("hi this is the number of staff followed by the last valuation date")
        console.log(this.items2[10][3])
        this.uploadModel.companyUploadValuationDate = this.items2[15][3]
        console.log(this.items2[11][3])
        this.uploadModel.companyUploadFirstValuationYear = this.items2[16][3]
        console.log(this.items2[12][3])
        this.uploadModel.companyUploadLastValuationYear = this.items2[17][3]
        console.log(this.items2[13][3])
        console.log(this.items2[14][3])
        console.log(this.items2[15][3])
        this.uploadModel.companyUploadLastValuationValue = this.items2[18][3]
        console.log(this.items2[16][3])
        this.uploadModel.companyUploadRaisingFund = this.items2[19][3]
        console.log(this.items2[17][3])
        this.uploadModel.companyUploadFundingNeeds = this.items2[20][3]
        console.log("hi" + " " + this.uploadModel.companyUploadDescription)
        this.$store.dispatch("companyUpload/createCompanyUpload", this.uploadModel)
      })

      let input = this.$refs.file;
      console.log(input);
      let files = input.files;
      this.fileName = files[0].name
      console.log(this.fileName);
      let size = files[0].size/1000000
      //console.log(size);
      if (size > 5) {
        swal("Error", "File size must not be more than 5mb, please try with a reduced file size", "error")
        return;
      }
      const reader = new FileReader;
      try {
        reader.onload = e => {
          this.base64 = e.target.result;
          console.log(e.target.result);
          this.uploadOfficerImage()
        };
        reader.readAsDataURL(files[0]);
        this.$emit('input', files[0]);
      } catch (e) {
        console.warn(e.message);
      }
    },
    async uploadOfficerImage() {
      this.showLoader = true;
      // this.model.username = `${this.name}_${this.auth.userInfo.customerUsername}_${Date.now()}`;
      this.model.base64 = this.base64;
      await this.$store.dispatch("sbucket/uploadEncodedFile", this.model, {root: true})
      this.url = this.sbucket.s3bucketResponse.url;
      console.log(this.url);
      this.showLoader = false;
    },
  },
  computed: {
    ...mapState(["auth","sbucket", "company"]),
  }
}
</script>

