<template>
  <home-layout>
    <div class="qoo-auth-section wf-section">
      <div class="qoo-auth-container">
        <lottie-player src="https://assets7.lottiefiles.com/datafiles/OhIfcxnkLsj1Jxj/data.json"  background="transparent"  speed="1"  style="width: 300px; height: 300px;" autoplay></lottie-player>
        <h1 class="auth-heading2">Successful</h1>
        <div class="auth-text-4 sub-heading"> Your payment was successful
        </div>
        <a href="#" class="auth-button success w-button" v-on:click="goToWallet">Proceed</a>
      </div>
    </div>
  </home-layout>
</template>

<script>
import HomeLayout from "@/views/layout/HomeLayout";
import RouterUtils from "@/utils/BaseUtils/RouterUtils";
import WalletRequest from "@/models/requests/WalletRequest";
// import LottiePlayer from 'lottie-player-vue';
export default {
  name: "PaymentSuccess",
  components: {HomeLayout},
  data(){
    return {
      RouterUtils: RouterUtils,
      balanceModel: WalletRequest.readBalance,
      transactionModel: WalletRequest.readTransaction
    }
  },
  methods: {
    goToWallet(){
      this.$store.dispatch("company/updateSettings", {})
      this.RouterUtils.changeRouteTo(
          this.RouterUtils.routes.company.SETTINGS.name
      );
    },
    getWalletDetails(){
      this.balanceModel.customerId = this.auth.userInfo.userId;
      this.transactionModel.customerId = this.auth.userInfo.userId;
      this.$store.dispatch("wallet/readBalance")
      this.$store.dispatch("wallet/readTransactions")
      this.RouterUtils.changeRouteTo(
          this.RouterUtils.routes.company.SETTINGS.name
      );
    },
    goToLogin() {
      this.$store.dispatch("company/updateSettings", {})
      this.RouterUtils.changeRouteTo(
          this.RouterUtils.routes.company.SETTINGS.name
      );
    }
  },
  mounted() {
    this.getWalletDetails()
  }
}
</script>

