<template>
  <home-layout>
    <div class="qoo-auth-section wf-section">
      <div class="qoo-auth-container">
        <lottie-player src="https://assets7.lottiefiles.com/datafiles/OhIfcxnkLsj1Jxj/data.json"  background="transparent"  speed="1"  style="width: 300px; height: 300px;" autoplay></lottie-player>
        <h1 class="auth-heading2">Successful</h1>
        <div class="auth-text-4 sub-heading"> {{ successMessage.start }} <br /> {{ successMessage.end }}
        </div>
        <a href="#" class="auth-button success w-button" v-on:click="goToLogin">Proceed</a>
      </div>
    </div>
  </home-layout>
</template>

<script>
import HomeLayout from "@/views/layout/HomeLayout";
import RouterUtils from "@/utils/BaseUtils/RouterUtils";
// import LottiePlayer from 'lottie-player-vue';
export default {
  name: "ResetPasswordSuccessPage",
  components: {HomeLayout},
  data(){
    return {
      RouterUtils: RouterUtils,
    }
  },
  methods: {
    goToLogin() {
      this.RouterUtils.changeRouteTo(this.RouterUtils.routes.company.LOGIN.name)
    }
  },
  computed: {
    successMessage() {
      let messageStart;
      let messageEnd;
      if (this.$route.path === "/reset-password/success") {
        messageStart = 'You have successfully changed';
        messageEnd = 'your password'
      } else if (this.$route.path === "/sign-up/success") {
        messageStart = 'You have successfully signed up';
        messageEnd = ''
      }
      else if (this.$route.path === "/change-password/success"){
        messageStart = 'Your password was changed successfully';
        messageEnd = ''
      }
      return {start: messageStart, end: messageEnd};
    }
  }
}
</script>

