<script>
import HomeLayout from "@/views/layout/HomeLayout";
import RouterUtils from "@/utils/BaseUtils/RouterUtils";
import { mapState } from "vuex";
import CompanyRequest from "@/models/requests/CompanyRequest";
import WalletRequest from "@/models/requests/WalletRequest";
import UserRequest from "@/models/requests/UserRequest";
export default {
  name: "ExplorePageUser",
  components: { HomeLayout },
  data() {
    return {
      RouterUtils: RouterUtils,
      model: CompanyRequest.readById,
      balanceModel: WalletRequest.readBalance,
      transactionModel: WalletRequest.readTransaction,
      displayCampaign: [],
      raising: false,
      featuring: true,
      usermodel: UserRequest.readById,
      displayCompany: [],
      items: CompanyRequest.readByVerificationStatus

    };
  },
  methods: {
    getWalletDetails(){
      this.balanceModel.customerId = this.auth.userInfo.userId;
      this.transactionModel.customerId = this.auth.userInfo.userId;
      this.$store.dispatch("wallet/readBalance")
      this.$store.dispatch("wallet/readTransactions")
    },
    showDetail() {
      this.RouterUtils.changeRouteTo(
        this.RouterUtils.routes.company.DETAILS.name
      );
    },
    goToCompany(event) {
      console.log(event)
      this.model.companyId = event

      this.$store.dispatch("company/readCompanyById", this.model)
      RouterUtils.changeRouteTo(
        RouterUtils.routes.company.PROFILE_PREVIEW.name
      );
    },
    goToCampaign(event) {
      console.log(event)
      this.model.campaignId = event
      this.$store.dispatch("campaign/readCampaignById", this.model)
      RouterUtils.changeRouteTo(
          RouterUtils.routes.company.CAMPAIGN_PREVIEW.name
      );
    },
    getCampaigns(){
      console.log(this.campaigns)
      this.campaigns.forEach((campaign) => {
        if (campaign.campaignVerificationStatus === "VERIFIED"){
          this.displayCampaign.push(campaign)
          console.log("this are the active campaigns")
        }
      })
    },
    getCompany(){
      console.log(this.companies)
      this.companies.forEach((company) => {
        if (company.companyVerificationStatus === 'VERIFIED'){
          console.log(company.companyId)
          this.displayCompany.push(company)
          console.log(this.displayCompany)
        }
      })
      console.log(this.displayCompany)
    },
    goToRaising(){
      this.raising = true;
      this.featuring = false
    },
    goToFeaturing(){
      this.raising = false;
      this.featuring = true
    },
    getTarget(target){
      return target.toLocaleString("en-US")
    }
  },
  computed: {
    ...mapState(["campaign", "auth"]),
    ...mapState({
      companies: (state) => state.company.companys.data,
      campaigns: (state) => state.campaign.campaigns.data,
    }),
    defaultCompanyLogoImage() {
      return require("../../assets/img/startup-logo.png");
    },
    defaultCompanyBannerImage() {
      return require("../../assets/img/img1.png");
    }
  },
  mounted() {
    this.getWalletDetails()
    this.$store.dispatch("company/readByVerificationStatus", this.items);
    this.$store.dispatch("campaign/readCampaign");

  },
  watch: {
    campaigns(){
      this.getCampaigns()
    },
    companies(){
      console.log(this.companies)
      this.getCompany()
      console.log(this.getCompany())
    }
  }
};
</script>

