          <div class="auth-form w-form">
            <form id="email-form" name="email-form" data-name="Email Form"
                  redirect="https://qoovest-dev.webflow.io/reset-password-password"
                  data-redirect="https://qoovest-dev.webflow.io/reset-password-password" method="get" class="form"
                  @submit.prevent="completeEnrollment">
              <!-- <div class="form-field-container email-verify"><input type="text" class="form-field email-verify w-input"
                  maxlength="1" name="field-2" data-name="Field 2" placeholder="" id="field-2" required=""><input
