</template>
<script>
import HomeLayout from "@/views/layout/HomeLayout";
    import RouterUtils from "@/utils/BaseUtils/RouterUtils";
    import AuthRequest from "@/models/requests/AuthRequest";
    import { mapState } from "vuex";

export default {
  name: "ResetPasswordOTP",
  components: {HomeLayout},
  data(){
    return {
      RouterUtils: RouterUtils,
      model: AuthRequest.completeEnrollment,
      resendModel: AuthRequest.resendOtp
    }
  },
  methods: {
    verifyEmail(){
      this.RouterUtils.changeRouteTo(this.RouterUtils.routes.company.RESET_PASSWORD.name)
    },
    handleOnComplete(otp) {
      this.model.otp = otp;
    },
    handleOnChange(otp) {
      this.model.otp = otp;
    },
    completeEnrollment() {
      this.model.email = localStorage.getItem("initEmail");
      this.model = localStorage.getItem("emailSignUp")
      this.$store.dispatch('auth/completeEnrollment', this.model);
      this.RouterUtils.changeRouteTo(this.RouterUtils.routes.auth.resetPassword.name)

    },
    resendOtp() {
      this.model = JSON.parse(localStorage.getItem("signUpBody"));
      this.resendModel.username = localStorage.getItem("email");
      this.$store.dispatch('auth/resendOtp', this.resendModel);
      if (this.auth.response.responseCode === '00'){
        document.querySelector('.resend-text').style.display = 'flex'
      }
    }
  },
  computed: {
    emailString() {
      let email = localStorage.getItem("email");
      if (email) {
        let beginningString = email.substring(0, 5);
        let emailClientIndex = email.indexOf("@");
        let emailClient = email.substring(emailClientIndex, (email.length));
        return `${beginningString}****${emailClient}`;
      } return "Idris****@gmail.com"
          // return localStorage.getItem("initEmail");
    },
    ...mapState(['auth'])
  }
}
</script>

