import CompanyUploadResponse from "@/models/response/CompanyUploadResponse";
import QuestionService from "@/services/QuestionService";
import CompanyUploadRequest from "@/models/requests/CompanyUploadRequest";
import BaseNotification from "@/components/baseNotification/BaseNotification";

export const state = {
    loading: false,
    question: CompanyUploadResponse.question,
    questions: CompanyUploadResponse.read
}

export const getters = {

}

export const mutations = {
    updateLoading: (state, payload) => {
        state.loading = payload;
    },
    updateQuestion: (state, payload) => {
        state.question = payload
    },
    updateQuestions: (state, payload) => {
        state.questions = payload;
    },
}

export const actions = {
    createQuestion({commit},payload = CompanyUploadRequest.create){
        commit("updateLoading", true)
        return QuestionService.callCreateApi(payload)
            .then(response => {
                commit("updateLoading", false)
                let responseData = response.data
                if(responseData.responseCode === "00"){
                    BaseNotification.fireToast("success", responseData.responseMessage).then()
                    commit('updateScreen', 'success')
                }else{
                    BaseNotification.fireToast("error", responseData.responseMessage).then()
                }
            })
            .catch(err=>{
                commit("updateLoading", false)
                console.error(err)
            })
    },
    updateInstruction: ({ commit }, payload = CompanyUploadRequest.read) => {
        commit('updateLoading', true)
        return QuestionService.callUpdateApi(payload).then(response => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateLoading", false)
                commit("updateInstruction", responseData)
                // BaseNotification.fireToast("success", responseData.responseMessage).then()

            } else {
                commit("updateLoading", false)
                BaseNotification.fireToast("error", responseData.responseMessage).then()
            }
        }).catch((error) => {
            commit("updateLoading", false)
            BaseNotification.fireToast("success", error).then()


        })

    },
    readInstruction({commit}, payload = CompanyUploadRequest.read) {
        return QuestionService.callReadApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateAddedInstruction", responseData)
            }
        })
    },
    deleteQuestion(){

    }
}
