import TestResponse from "@/models/response/TestResponse";
import TestService from "@/services/TestService";
import TestRequest from "@/models/requests/TestResquest";
import BaseNotification from "@/components/baseNotification/BaseNotification";

export const state = {
    loading: false,
    test: TestResponse.test,
    tests: TestResponse.read
}

export const getters = {

}

export const mutations = {
    updateLoading: (state, payload) => {
        state.loading = payload;
    },
    updateTest: (state, payload) => {
        state.test = payload
    },
    updateTests: (state, payload) => {
        state.tests = payload;
    },
}

export const actions = {
    createTest({commit},payload = TestRequest.create){
        commit("updateLoading", true)
        return TestService.callCreateApi(payload)
            .then(response => {
                commit("updateLoading", false)
                let responseData = response.data
                if(responseData.responseCode === "00"){
                    BaseNotification.fireToast("success", responseData.responseMessage).then()
                    commit('updateScreen', 'success')
                }else{
                    BaseNotification.fireToast("error", responseData.responseMessage).then()
                }
            })
            .catch(err=>{
                commit("updateLoading", false)
                console.error(err)
            })
    },
    updateTest(){

    },
    readTest(){

    },
    deleteTest(){

    }
}
