<script>
import HomeLayout from "@/views/layout/HomeLayout";
import RouterUtils from "@/utils/BaseUtils/RouterUtils";
import AuthRequest from "@/models/requests/AuthRequest";
// import vue3PhoneInput from "vue3-phone-input";
import { countryCodes } from "@/utils/country/countryCodes";
import { mapState } from "vuex";
import AddressComponent from "../shared/AddressComponent.vue";
import PhoneNumberInput from "../shared/PhoneNumberInput.vue";
import CheckBox from "../shared/CheckBox.vue";
import { reactive } from "vue";

export const store = reactive({
  openOptions: {
    provinces: false,
    phone: false,
  },
  closeAll() {
    this.openOptions = {
      provinces: false,
      phone: false,
    };
  },
  openSingleOption(name) {
    this.openOptions[name] = true;
  },
});

export default {
  name: "SignUpPage",
  // components: { HomeLayout, vue3PhoneInput, AddressComponent },
  components: { HomeLayout, AddressComponent, PhoneNumberInput, CheckBox },

  data() {
    return {
      RouterUtils: RouterUtils,
      model: AuthRequest.initiateEnrollment,
      phone: {},
      termsAndConditions: false,
      termsError: false,
    };
  },
  methods: {
    termsValue(input) {
      this.termsAndConditions = input;
    },
    goToLogin() {
      this.RouterUtils.changeRouteTo(
          this.RouterUtils.routes.company.LOGIN.name
      );
    },
    initiateEnroll() {
      this.termsError = false;
      this.$forceUpdate();
      this.model.country = "Canada";
      this.model.countryCode = "1";
      if (this.termsAndConditions) {
        localStorage.setItem("emailSign", this.model.email);
        this.$store.dispatch("auth/initiateEnrollment", this.model);
      } else {
        this.termsError = true;
        setTimeout(() => {
          this.termsError = false;
        }, 3000);
      }

      // this.$store.commit("auth/updateInitEmail", this.model.email);
      // this.RouterUtils.changeRouteTo(this.RouterUtils.routes.company.OTP.name);
    },
    setCountry(code) {
      let countryName;
      countryCodes.forEach((country) => {
        if (country["dial_code"] === `+${code}`) {
          countryName = country["name"];
        }
      });
      return countryName;
    },
    updateAddress(addressObject) {
      this.model.address = addressObject.address;
      this.model.province = addressObject.province;
      this.$forceUpdate();
    },
    updateNumber(input) {
      this.model.phone = input.number;
      this.model.country = input.country;
      this.$forceUpdate();
    },
    closeAllOptions() {
      store.closeAll();
    },
  },
  computed: {
    ...mapState(["auth"]),
    signupUnsuccessful() {
      let output = false;
      if (this.auth.response.responseCode !== "00") {
        output = true;
      }
      return output;
    },
  },
};
</script>

