import client from "./BaseService";

export default {
    callCreateApi(payload) {
        return client.apiClient.post("/company-key/create", payload);
    },
    callReadApi(payload) {
        return client.apiClient.post("/company-key/read", payload);
    },
    callReadByIdApi(payload) {
        return client.apiClient.post("/company-key/read-by-company-id", payload);
    },
    callUpdateApi(payload){
        return client.apiClient.post("/company-key/update", payload);
    },
    callDeleteApi(payload){
        return client.apiClient.post("/company-key/delete", payload);
    }

}