<template>
  <div class="custom-accordion-item">
    <div @click="toggleAccordion()" class="custom-accordion-header">
      <h3 class="">{{ title }}</h3>
      <button class="custom-accordion-button">
        <img
          :class="{ rotate: accordionOpen }"
          src="@/assets/img/accordion.svg"
          alt=""
        />
      </button>
    </div>
    <div
      v-show="accordionOpen"
      :class="['custom-accordion-content', contentStyle]"
      id="content-accordion"
      :style="contentStyle"
    >
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "AccordionItem",
  props: {
    title: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      accordionOpen: false,
    };
  },
  computed: {
    contentStyle() {
      let output = {
        open: false,
      };
      if (this.accordionOpen) {
        output = {
          open: true,
        };
      }
      return output;
    },
  },
  methods: {
    toggleAccordion() {
      this.accordionOpen = this.accordionOpen ? false : true;
      console.log("accordionValue", this.accordionOpen);
    },
  },
};
</script>

<style scoped>
.custom-accordion-item {
  border-top: solid 1px rgba(194, 209, 217, 0.26);
  border-bottom: solid 1px rgba(194, 209, 217, 0.26);
  transition: all 0.2s;
}

.custom-accordion-header {
  display: flex;
  justify-content: space-between;
  padding: 20px 0;
  cursor: pointer;
}

.custom-accordion-header > h3 {
  font-size: 18px;
  color: #00325c;
  font-weight: 500;
}

.custom-accordion-button img {
  transition: all 0.2s ease-in-out;
}

.custom-accordion-button .rotate {
  transform: rotate(45deg);
}

.custom-accordion-content {
  margin: 20px 0;
  height: 0;
  transition: all 0.5s ease-in-out;
}

.open.custom-accordion-content {
  height: 100%;
}
</style>
