import client from "./BaseService";

export default {
    callCreateApi(payload) {
        return client.apiClient.post("/projection/create", payload);
    },
    callReadApi(payload) {
        return client.apiClient.post("/projection/read", payload);
    },
    callReadByIdApi(payload) {
        return client.apiClient.post("/projection/read-by-company-id", payload);
    },
    callUpdateApi(payload){
        return client.apiClient.post("/projection/update", payload);
    },
    callDeleteApi(payload){
        return client.apiClient.post("/projection/delete", payload);
    }

}