import {apiClient} from "./BaseService";

export default {
    callCreateApi(payload) {
        return apiClient.appClient.post("/story/create", payload);
    },
    callReadApi(payload) {
        return apiClient.appClient.post("/story/read", payload);
    },
    callUpdateApi(payload){
        return apiClient.appClient.post("/story/update", payload);
    },
    callDeleteApi(payload){
        return apiClient.appClient.post("/story/delete", payload);
    }

}