import client from "./BaseService";

export default {
    callCreateApi(payload) {
        return client.apiClient.post("/competition/create", payload);
    },
    callReadApi(payload) {
        return client.apiClient.post("/competition/read", payload);
    },
    callReadByIdApi(payload) {
        return client.apiClient.post("/competition/read-by-company-id", payload);
    },
    callUpdateApi(payload){
        return client.apiClient.post("/competition/update", payload);
    },
    callDeleteApi(payload){
        return client.apiClient.post("/competition/delete", payload);
    }

}