import KeynoteResponse from "@/models/response/KeynoteResponse";
import KeynoteService from "@/services/KeynoteService";
import KeynoteRequest from "@/models/requests/KeynoteRequest";
import BaseNotification from "@/components/baseNotification/BaseNotification";

export const state = {
    loading: false,
    keynote: KeynoteResponse.readById,
    keynotes: KeynoteResponse.read,

}

export const getters = {

}

export const mutations = {
    updateLoading: (state, payload) => {
        state.loading = payload;
    },
    updateKeynote: (state, payload) => {
        state.keynote = payload
    },
    updateKeynotes: (state, payload) => {
        state.keynotes = payload;
    },
}

export const actions = {
    createKeynote({commit},payload = KeynoteRequest.create){
        commit("updateLoading", true)
        return KeynoteService.callCreateApi(payload)
            .then(response => {
                commit("updateLoading", false)
                let responseData = response.data
                if(responseData.responseCode === "00"){
                    BaseNotification.fireToast("success", responseData.responseMessage).then()
                    commit('updateScreen', 'success')
                }else{
                    BaseNotification.fireToast("error", responseData.responseMessage).then()
                }
            })
            .catch(err=>{
                commit("updateLoading", false)
                console.error(err)
            })
    },
    updateKeynote: ({ commit }, payload = KeynoteRequest.read) => {
        commit('updateLoading', true)
        return KeynoteService.callUpdateApi(payload).then(response => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateLoading", false)
                commit("updateInstruction", responseData)
                // BaseNotification.fireToast("success", responseData.responseMessage).then()

            } else {
                commit("updateLoading", false)
                BaseNotification.fireToast("error", responseData.responseMessage).then()
            }
        }).catch((error) => {
            commit("updateLoading", false)
            BaseNotification.fireToast("success", error).then()
        })

    },
    readKeynote({commit}, payload = KeynoteRequest.read) {
        return KeynoteService.callReadApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateAddedInstruction", responseData)
            }
        })
    },
    readKeynoteById({commit}, payload = KeynoteRequest.readById) {
        return KeynoteService.callReadByIdApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateKeynote", responseData)
                console.log(state.keynote)
                // RouterUtils.changeRouteTo(RouterUtils.routes.key.DASHBOARD.name)
            }
        })
    },
    deleteKeynote(){

    }
}
