<template>
  <home-layout>
    <div class="qoo-auth-section wf-section">
      <div class="qoo-auth-container">
        <a href="../index.html" class="w-inline-block"><img src="../../assets/img/Group-28.png" loading="lazy" width="150" alt="" class="image-3 sign-up"></a>
        <h1 class="auth-heading1-2">Reset Password</h1>
        <div class="auth-text-4 sub-heading">Reset the passcode for your account, this will be used for logging in.</div>
        <div class="auth-form w-form">
          <form id="email-form" name="email-form" method="get" @submit.prevent="completeForgetPassword">
            <div class="form-field-container">
              <input type="password" class="form-field sign-up w-input" maxlength="256" name="field-2" data-name="Field 2" placeholder="New Password" id="field-2" required="" v-model="model.password"></div>
            <div class="form-field-container">
              <input type="password" class="form-field sign-up w-input" maxlength="256" name="field-2" data-name="Field 2" placeholder="Confirm Password" id="field-2" required="" v-model="model.passwordConfirmation"></div>
            <button class="auth-button reset w-button" v-if="!auth.authLoading">Proceed</button>
            <div class="loader" v-else></div>
          </form>
          <div class="w-form-done">
            <div>Thank you! Your submission has been received!</div>
          </div>
          <div class="w-form-fail">
            <div>Oops! Something went wrong while submitting the form.</div>
          </div>
        </div>
      </div>
    </div>
  </home-layout>
</template>

<script>
import HomeLayout from "@/views/layout/HomeLayout";
import RouterUtils from "@/utils/BaseUtils/RouterUtils";
import AuthRequest from "@/models/requests/AuthRequest";
import {mapState} from "vuex";
export default {
  name: "ResetPasswordPage",
  components: {HomeLayout},
  data(){
    return {
      RouterUtils: RouterUtils,
      model: AuthRequest.completeResetPassword
    }
  },
  methods: {
    completeForgetPassword() {
      this.model.username = localStorage.getItem("email");
      this.model.otp = localStorage.getItem("OTP");
      this.model.countryCode = "234"
      this.$store.dispatch("auth/completeResetPassword", this.model);
    },
    checkSuccessful(){
      this.RouterUtils.changeRouteTo(this.RouterUtils.routes.company.RESET_PASSWORD_SUCCESS.name)
    }
  },
  computed:{
    ...mapState(["auth"])
  }
}
</script>

