import {apiClient} from "./BaseService";

export default {
    callCreateApi(payload) {
        return apiClient.appClient.post("/question/create", payload);
    },
    callReadApi(payload) {
        return apiClient.appClient.post("/question/read", payload);
    },
    callUpdateApi(payload){
        return apiClient.appClient.post("/question/update", payload);
    },
    callDeleteApi(payload){
        return apiClient.appClient.post("/question/delete", payload);
    }

}