<template>
  <div>
    <province-component @updateModel="(value) => updateProvince(value)" />
    <div class="form-field-container">
      <input
        type="text"
        class="form-field sign-up w-input"
        maxlength="256"
        name="field-2"
        data-name="Field 2"
        placeholder="Address Line"
        id="field-2"
        required=""
        v-model="address.address"
        @input="updateAddress()"
      />
    </div>
  </div>
</template>

<script>
import ProvinceComponent from "../shared/ProvinceComponent.vue";
export default {
  name: "AddressComponent",
  components: { ProvinceComponent },
  data() {
    return {
      address: {
        address: "",
        province: "",
      },
    };
  },
  methods: {
    updateAddress() {
      this.$emit("updateAddress", this.address);
      this.$forceUpdate();
    },
    updateProvince(input) {
      this.address.province = input;
      this.updateAddress();
      this.$forceUpdate();
    },
  },
};
</script>

<style scoped>
* {
  user-select: none;
}
</style>
