<template>
  <div class="target-input form-field w-input">
    <div class="currency-mark">
      <img
          src="https://flagcdn.com/16x12/ca.png"
          srcset="
          https://flagcdn.com/32x24/ca.png 2x,
          https://flagcdn.com/48x36/ca.png 3x
        "
          width="16"
          height="12"
          alt="Canada"
      />
      <span>CA$</span>
    </div>
    <input
        type="number"
        class="add"
        maxlength="256"
        name="address"
        data-name="Name"
        placeholder="Enter your campaign target"
        min="50000"
        max="5000000"
        value="targetValue"
        @input="inputTarget"
        required
    />
    <div class="error" v-show="errorState">
      Campaign Target must be up than CA$50,000 and not more than CA$5,000,000.
    </div>
  </div>
</template>

<script>
const formatter = new Intl.NumberFormat("en-US", {
  style: "currency",
  currency: "CAD",

  // These options are needed to round to whole numbers if that's what you want.
  //minimumFractionDigits: 0, // (this suffices for whole numbers, but will print 2500.10 as $2,500.1)
  //maximumFractionDigits: 0, // (causes 2500.99 to be printed as $2,501)
});

export default {
  name: "CampaignTargetInput",
  data() {
    return {
      target: 0,
      errorState: false,
    };
  },
  computed: {
    targetValue() {
      return formatter.format(this.target);
    },
  },
  watchers: {
    target(newTarg) {
      if (newTarg < 50000) {
        this.target = 50000;
      }
    },
  },
  methods: {
    inputTarget(e) {
      this.target = e.target.value;
      this.$emit('campaign-target', this.target)
      if (this.target < 50000 || this.target > 5000000) {
        this.errorState = true;
      } else {
        this.errorState = false;
      }
    },
  },
};
</script>

