<template>
  <home-layout>
    <div class="qoo-auth-section wf-section">
      <div class="qoo-auth-container">
        <a href="" class="w-inline-block"><img src="../../assets/img/Group-28.png" loading="lazy" width="150" alt="" class="image-3 sign-up"></a>
        <h1 class="auth-heading1-2">Change Password </h1>
        <div class="auth-form w-form">
          <form id="email-form" name="email-form" data-name="Email Form" @submit.prevent="changePassword">
            <div class="form-field-container login"><input type="password" class="form-field sign-up w-input" maxlength="256" name="field-2" data-name="Field 2" placeholder="Old password" id="field-2" required="" v-model="model.oldPassword"></div>
            <div class="form-field-container login"><input type="password" class="form-field sign-up w-input" maxlength="256" name="field-2" data-name="Field 2" placeholder="New Password" id="field-3" required="" v-model="model.password"></div>
            <div class="form-field-container login"><input type="password" class="form-field sign-up w-input" maxlength="256" name="field-2" data-name="Field 2" placeholder="Confirm Password" id="field-4" required="" v-model="model.passwordConfirmation"></div>
            <button v-if="!auth.authLoading" :disabled="disabled" type="submit" class="auth-button reset w-button">Save Changes</button>
            <div class="loader" v-else></div>
            <!--            <button type="submit">submit</button>-->
          </form>
          <div class="w-form-done">
            <div>Thank you! Your submission has been received!</div>
          </div>
          <div class="w-form-fail">
            <div>Oops! Something went wrong while submitting the form.</div>
          </div>
        </div>
      </div>
    </div>
  </home-layout>
</template>

<script>
import HomeLayout from "@/views/layout/HomeLayout";
import RouterUtils from "@/utils/BaseUtils/RouterUtils";
import AuthRequest from "@/models/requests/AuthRequest";
import {mapState} from "vuex";
export default {
  name: "ChangePassword",
  components: {HomeLayout},
  data(){
    return {
      RouterUtils: RouterUtils,
      model: AuthRequest.changePassword
    }
  },
  methods: {
    changePassword(){
      console.log(this.auth.userInfo)
      this.model.username = localStorage.getItem('loginEmail');
      this.model.countryCode = "234";
      this.$store.dispatch("auth/changePassword", this.model)
    }
  },
  computed:{
    ...mapState(["auth"])
  }
}
</script>

