import client from "./BaseService";

export default {
    callCreateApi(payload) {
        return client.apiClient.post("/traction/create", payload);
    },
    callReadApi(payload) {
        return client.apiClient.post("/traction/read", payload);
    },
    callReadByIdApi(payload) {
        return client.apiClient.post("/traction/read-by-company-id", payload);
    },
    callUpdateApi(payload){
        return client.apiClient.post("/traction/update", payload);
    },
    callDeleteApi(payload){
        return client.apiClient.post("/traction/delete", payload);
    }

}