import ProjectionResponse from "@/models/response/ProjectionResponse";
import ProjectionService from "@/services/ProjectionService";
import ProjectionRequest from "@/models/requests/ProjectionRequest";
import BaseNotification from "@/components/baseNotification/BaseNotification";

export const state = {
    loading: false,
    projection: ProjectionResponse.readById,
    projections: ProjectionResponse.read,

}

export const getters = {

}

export const mutations = {
    updateLoading: (state, payload) => {
        state.loading = payload;
    },
    updateProjection: (state, payload) => {
        state.projection = payload
    },
    updateStories: (state, payload) => {
        state.projections = payload;
    },
}

export const actions = {
    createProjection({commit},payload = ProjectionRequest.create){
        commit("updateLoading", true)
        return ProjectionService.callCreateApi(payload)
            .then(response => {
                commit("updateLoading", false)
                let responseData = response.data
                if(responseData.responseCode === "00"){
                    BaseNotification.fireToast("success", responseData.responseMessage).then()
                    commit('updateScreen', 'success')
                }else{
                    BaseNotification.fireToast("error", responseData.responseMessage).then()
                }
            })
            .catch(err=>{
                commit("updateLoading", false)
                console.error(err)
            })
    },
    updateProjection: ({ commit }, payload = ProjectionRequest.read) => {
        commit('updateLoading', true)
        return ProjectionService.callUpdateApi(payload).then(response => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateLoading", false)
                commit("updateInstruction", responseData)
                // BaseNotification.fireToast("success", responseData.responseMessage).then()

            } else {
                commit("updateLoading", false)
                BaseNotification.fireToast("error", responseData.responseMessage).then()
            }
        }).catch((error) => {
            commit("updateLoading", false)
            BaseNotification.fireToast("success", error).then()
        })

    },
    readProjection({commit}, payload = ProjectionRequest.read) {
        return ProjectionService.callReadApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateAddedInstruction", responseData)
            }
        })
    },
    readProjectionById({commit}, payload = ProjectionRequest.readById) {
        return ProjectionService.callReadByIdApi(payload).then((response) => {
            let responseData = response.data
            if (responseData.responseCode === "00") {
                commit("updateProjection", responseData)
                console.log(state.projection)
                // RouterUtils.changeRouteTo(RouterUtils.routes.projection.DASHBOARD.name)
            }
        })
    },
    deleteProjection(){

    }
}
