                          <div class="text-block-21">Stay tuned for something interesting.</div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </home-layout>
</template>

<script>
import HomeLayout from "@/views/layout/HomeLayout";
import RouterUtils from "@/utils/BaseUtils/RouterUtils";
import {mapState} from "vuex";
import WalletRequest from "@/models/requests/WalletRequest";
import CompanyWallet from "@/components/wallet/companyWallet";
import S3Request from "@/models/requests/S3Request";
import BaseLoader from "@/components/loader/BaseLoader";
import swal from "sweetalert";
import UserRequest from "@/models/requests/UserRequest";

export default {
  name: "AccountSettingPage",
  components: {CompanyWallet, HomeLayout, BaseLoader},
  data(){
    return {
      model: WalletRequest.create,
      RouterUtils: RouterUtils,
      settings: true,
      portfolio: false,
      wallets: false,
      wishlist: false,
      portfolioEmpty: true,
      walletEmpty: true,
      wishListEmpty: true,
      token: null,
      file: null,
      loading: true,
      fileName: null,
      awaitingUpload: true,
      downloadUpload: false,
      finishUpload: false,
      value: "dashboard",
      uploadPercentage: 0,
      selectedFiles: undefined,
      currentFile: undefined,
      items: [],
      url:"",
      disableInput: false,
      showLoader: false,
      base64: "",
      models: S3Request.prototype.uploadBase64(),
      items2: [],
      userModel: UserRequest.update,
    }
  },
  methods: {
    checkSuccessful() {
      this.userModel.userId = this.auth.userInfo.userId
      this.userModel.userIdentificationCard = this.url
      this.$store.dispatch('user/updateUser', this.userModel)
      this.url = ''
      this.closed('.logo1')
    },
    toggleModal(){
      if (document.querySelector('.reduce').style.display === "none"){
        document.querySelector('.reduce').style.display = "flex"
        document.querySelector('.wait').style.display = "none"
      }
      else {
        document.querySelector('.reduce').style.display = "none"
      }
    },
    toggleTransferModal(){
      if (document.querySelector('.wait').style.display === "none"){
        document.querySelector('.wait').style.display = "block"
        document.querySelector('.reduce').style.display = "none"
      }
      else {
        document.querySelector('.wait').style.display = "none"
      }
    },
    goToSettings(){
      this.settings = true;
      this.portfolio = false;
      this.wallets = false;
      this.wishlist = false
    },
    goToPortfolio(){
      this.settings = false;
      this.portfolio = true;
      this.wallets = false;
      this.wishlist  = false
    },
    goToWallet(){
      this.settings = false;
      this.portfolio = false;
      this.wallets = true;
      this.wishlist = false
    },
    goToWishlist(){
      this.settings = false;
      this.portfolio = false;
      this.wallets = false;
      this.wishlist = true
    },
    checkState(){
      document.getElementById('Address').disabled = document.getElementById('Address').value !== "";
      if (document.getElementById('DOB').value === ""){
        document.getElementById('DOB').disabled = false
      }
      else {
        document.getElementById('DOB').disabled = true
      }
    },
    openModal(){
      this.model.customerId = this.auth.userInfo.userId;
      this.$store.dispatch("wallet/createWallet", this.model)
      document.querySelector('.my-button').style.display = "none"
      document.querySelector('.loader').style.display = "block"
      RouterUtils.changeRouteTo(RouterUtils.routes.company.PAYMENT.name);
      // document.querySelector('.firstdisplay').style.display = 'none';
      // document.querySelector('.walletpaymentmodal').style.display = 'block';
    },
    close(){
      document.querySelector('.walletmodal').style.display = 'none';
    },
    topUp(){
      document.querySelector('.walletmodal').style.display = 'flex';
    },
    openCity(evt, cityName, id) {
      // Declare all variables
      let i, tabcontent, tablinks;

      // Get all elements with class="tabcontent" and hide them
      tabcontent = document.getElementsByClassName("content");
      for (i = 0; i < tabcontent.length; i++) {
        document.getElementsByClassName("content")[i].style.display = "none";
      }

      // Get all elements with class="tablinks" and remove the class "active"
      tablinks = document.getElementsByClassName("qoo-tabs");
      for (i = 0; i < tablinks.length; i++) {
        document.getElementsByClassName("qoo-tabs")[i].className = tablinks[i].className.replace(" w--current", "");
      }
      // Show the current tab, and add an "active" class to the button that opened the tab
      document.getElementById(cityName).style.display = "block";
      // document.getElementById(dataset.id).)
      document.getElementById(id).className += " w--current";

    },
    goToChangePassword(){
      RouterUtils.changeRouteTo(RouterUtils.routes.company.CHANGE_PASSWORD.name);
    },
    uploadImageFile(imageType) {
      // console.log(this.$refs["filelogo"]);
      let refName = imageType.slice(0, 4);
      let input = this.$refs["file" + refName];
      // console.log("input", input, "imageType", imageType, "refName", refName);
      let files = input.files;
      this.fileName = files[0].name;
      if (files[0] !== null) {
        this.awaitingUpload = false;
        this.downloadUpload = true;
        setTimeout(() => {
          document.querySelector(".progress-bar").style.width = "50%";
          document.getElementById("status").innerHTML = "50%";
        }, 2000);
        setTimeout(() => {
          document.querySelector(".progress-bar").style.width = "100%";
          document.getElementById("status").innerHTML = "100%";
        }, 3000);
        setTimeout(() => {
          this.downloadUpload = false;
          this.finishUpload = true;
        }, 4000);
      }
      let size = files[0].size / 1000000;

      if (size > 5) {
        swal(
            "Error",
            "File must not be moer than 5mb, please try with a reduced file size",
            "error"
        );
        return;
      }
      const reader = new FileReader();

      try {
        reader.onload = (e) => {
          this.base64 = e.target.result;
          this.uploadOfficerImagee(imageType);
        };
        reader.readAsDataURL(files[0]);
        this.$emit("input", files[0]);
      } catch (e) {
        console.warn(e.message);
      }
    },
    async uploadOfficerImagee(type) {
      this.showLoader = true;
      // this.model.username = `${this.name}_${this.auth.userInfo.customerUsername}_${Date.now()}`;
      this.models.username = `${
          this.auth.userInfo.userFirstName + this.auth.userInfo.userLastName
      }_${Date.now()}${type}`;
      this.models.base64 = this.base64;
      await this.$store.dispatch("sbucket/uploadEncodedFile", this.models, {
        root: true,
      });
      this.url = this.sbucket.s3bucketResponse.url;
      this.showLoader = false;
    },
    selectFile() {
      this.selectedFiles = this.$refs.file.files;
    },
    closed(className){
      document.querySelector(className).style.display='none';
      this.awaitingUpload = true
    },
    uploadProfile(className){
      document.querySelector(className).style.display='flex'
    },
  },
  mounted(){
    // this.checkState()
    console.log(this.company.settings)
    if (this.company.settings === 'wallet'){
      this.wallets = true;
      this.settings = false;
      this.portfolio = false;
      this.wishlist = false
    }

  },
  watch: {
    changeValueFunction(){
      //this.value = this.changeValue
    }
  },
  computed:{
    ...mapState(["auth", "company", "wallet", "sbucket"]),
    userInfo: state => state.auth.userInfo
  }
}
</script>

