<template>
  <div class="checkbox-container">
    <label :class="{ checkedLabel: checked }" for="term">
      <input v-model="checked" id="term" type="checkbox" @change="emitChange" />
      <img src="@/assets/img/small-tick.svg" alt="" />
    </label>
  </div>
</template>

<script>
export default {
  name: "CheckBox",
  emits: ["checkchange"],
  data() {
    return {
      checked: false,
    };
  },
  methods: {
    emitChange() {
      this.$emit("checkchange", this.checked);
    },
  },
};
</script>

<style scoped>
label.checkedLabel {
  background: rgba(93, 201, 160, 1);
  border: none;
}

#term {
  opacity: 0 !important;
  width: 0 !important;
  height: 0 !important;
  position: absolute;
}

.checkbox-container {
  width: 25px !important;
  height: 25px !important;
  display: flex;
  cursor: pointer;
}

.checkbox-container:hover {
  cursor: pointer;
}

label {
  border: solid 1px rgba(216, 218, 229, 1);
  /*width: 100%;*/
  /*height: 100%;*/
  margin-bottom: 0;
  width: 25px;
  height: 25px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

img {
  display: none;
  width: 11px;
  cursor: pointer;
}

input:checked + img {
  display: block;
}
</style>
